import Vue from 'vue'
import fetcher from '@/utils/fetch'
import VueLazyload from 'vue-lazyload'

Vue.use(VueLazyload)

const components = [
  require('./footer/footer.vue'),
  require('./video/Video.vue'),
  // require('./typeFilter/index.vue'),
  require('./filter/filter.vue'),
  require('./navigation/navigation.vue'),
  require('./header/index.vue'),
  require('./slide/slide.vue'),
  require('./layout/layout.vue'),
  require('./layout/layout-title.vue'),
  require('./layout/layout-item.vue'),
  require('./video/right-panel/right-grid-panel.vue'),
  require('./video/right-panel/right-list-panel.vue'),
  require('./video/share-dialog.vue'),
  require('./paging/paging.vue'),
  require('./carousel/carousel.vue'),
  require('./carousel/carousel-item.vue'),
  require('./backTop/back-top.vue'),
  require('./common/scroll-bar/vue-scrollbar.vue'),
  require('./common/scroll-bar/vertical-scrollbar.vue'),
]

components.forEach((component) => {
  Vue.component(component.name, component)
})

// ????????????fetch
Vue.prototype.$fetch = fetcher



