<template>
<li
  :class="$style.carouselItem"
  :style="{
    width: `${width}px`,
    height: `${height}px`,
  }"
>
  <a
    :href="`play.html?videoId=${item.videoBaseId}`"
    :class="$style.carouselItemLink"
    class="u-bg--default"
    target="_blank"
    :style="{
      height: `${height}px`,
    }"
  >
    <img :class="$style.carouselItemImg" v-lazy="adaptImgUrl(getImgUrl(item.images), width, height)" alt="">
    <span v-if="itemIndex && itemIndex <= 3" :class="$style.carouselItemRank">
      <i class="iconfont icon-billboard"></i>
      <strong>{{ itemIndex }}</strong>
    </span>
    <span
      v-if="item.extMap.sign"
      :class="[$style.carouselItemSub, $style.carouselItemSubrt]"
    >
      <img :src="signIcon(item.extMap.sign)" alt="">
    </span>
    <span
      v-if="item.extMap.updateEpisode"
      :class="[$style.carouselItemSub, $style.carouselItemSubrb]"
    >
      {{ item.extMap.updateEpisode }}
    </span>
    <span
      v-if="item.extMap.clarity"
      :class="[$style.carouselItemSub, $style.carouselItemSublb]"
    >
      {{ item.extMap.clarity }}
    </span>
    <div
      class="iconfont icon-play1"
      :class="$style.carouselItemMask"
      :style="{
        fontSize: `${iconSize}px`,
        height: `${height}px`,
        paddingTop: `${(height - iconSize) / 2}px`
      }"
    >
    </div>
  </a>
  <div :class="$style.carouselItemTitle">
    <a :href="`play.html?videoId=${item.videoBaseId}`" target="_blank">{{ item.name }}</a>
  </div>
  <div :class="$style.carouselItemCaption">{{ item.subName }}</div>
</li>
</template>

<script>
import commonMixins from '@/mixins/common'

export default {
  name: 'e-carousel-item',

  mixins: [commonMixins],

  props: {
    item: {},
    itemIndex: 0,
    width: {
      type: Number,
      default: 214
    },
    height: {
      type: Number,
      default: 321
    },
    iconSize: {
      type: Number,
      default: 64
    },
  },
  data () {
    return {
      // defaultImg: require('../../assets/index/layout/1.jpg')
      defaultImg: '',
    }
  },
  computed: {
  },
  methods: {
    getImgUrl(images) {
      let ret, arr
      arr = images.filter(image => image.scale === 3)
      ret = arr[0] ? this.prefixImgUrl(arr[0].url, '400') : this.defaultImg
      return ret
    },
    signIcon(sign) {
      const signMap = {
        '??????': require('../../assets/common/only-play.svg'),
        '??????': require('../../assets/common/self-made.svg'),
      }
      return signMap[sign]
    }
  },
}
</script>

<style lang="postcss" module>
.carouselItem {
  float: left;
  .carouselItemLink {
    position: relative;
    float: left;
    width: 100%;
    overflow: hidden;
    cursor: pointer;
    &:hover {
      :global(.iconfont) {
        display: block;
      }
    }
    .carouselItemImg {
      float: left;
      width: 100%;
      height: 100%;
    }
    .carouselItemRank {
      i {
        display: block;
        position: absolute;
        left: 8px;
        top: 8px;
        color: #ED743A;
        font-size: 48px;
      }
      strong {
        position: absolute;
        width: 48px;
        height: 55px;
        line-height: 55px;
        left: 8px;
        top: 8px;
        color: #fff;
        font-size: 22px;
        font-weight: normal;
        text-align: center;
      }
    }
    .carouselItemSub {
      position: absolute;
      display: block;
      padding: 4px;
      background: rgba(0, 0, 0, 0.7);
      border-radius: 3px;
      color: #fff;
      font-size: 12px;
      &rt {
        right: 0;
        top: 0;
        padding: 0;
        background: none;
      }
      &rb {
        right: 5px;
        bottom: 5px;
      }
      &lb {
        left: 5px;
        bottom: 5px;
      }
    }
    .carouselItemMask {
      display: none;
      position: absolute;
      width: 100%;
      background: rgba(0, 0, 0, 0.4);
      color: #F3BC29;
      text-align: center;
    }
  }
  .carouselItemTitle {
    float: left;
    width: 100%;
    line-height: 30px;
    font-size: 16px;
    /*font-weight: bold;*/
    color: #292929;
    overflow: hidden;
    /*text-overflow: ellipsis;*/
    white-space: nowrap;
    &:hover {
      color: #FABE00;
    }
  }
  .carouselItemCaption {
    float: left;
    width: 100%;
    line-height: 20px;
    font-size: 12px;
    color: #9D9D9D;
    overflow: hidden;
    /*text-overflow: ellipsis;*/
    white-space: nowrap;
  }
}
</style>



