export default function (fn, threshold) {
  let last
  let timer

  return function(...args) {
    const context = this

    if (last && Date.now() < last + threshold) {
      // ??????????????????????????????
      clearTimeout(timer)

      timer = setTimeout(() => {
        last = Date.now()
        fn.apply(context, args)
      }, threshold)
    } else {
      // ???????????????
      last = Date.now()
      fn.apply(context, args)
    }
  }
}



