<template>
<header :class="[$style.nav, fadeIn ? [$style.fadeIn]: '',  hide ? $style.hide: '']">
  <div :class="$style.navContainer">
    <a href='/' :class="$style.logo"></a>
    <div :class="$style.channel" v-if='showChannel'>
      <i :class="[$style.channelIcon, 'iconfont', 'icon-menu']"></i>
      <span>??????</span>
      <div :class="$style.triangle"></div>
      <div :class="$style.channelBox">
        <ul>
          <li v-for="(item, index) in channels"><a :href="`/?channelId=${item.id}`">{{item.name}}</a></li>
          <!-- <li :class="$style.highlight"><a href="#">??????</a></li> -->
        </ul>
      </div>
    </div>
    <div :class="[$style.searchContainer, 'u-clearfix']">
      <div :class="[$style.searchBoxContainer]">
        <form action="/search.html" method="get" target="_blank">
          <input type="hidden" name="q" :value="url">
          <input type="text" v-model='keyword' maxlength='20' @keydown='handleKeyDown' @input='getSuggestion' :class="[$style.searchBox, uiType == 'player' ? $style.bgGray: '']" @focus='focused=true' @blur='handleSearchBlur' :placeholder="videoName">
          <div :class="$style.searchTip" v-show='focused && searchStatus == 0 && keyword !== null && (searchHistory.length > 0 || hotList.length > 0)'>
            <div :class="$style.hisTitle" v-show='searchHistory.length > 0'><span :class="$style.hisLeft">????????????</span><i class="icon-delete iconfont" @click='clearHistory'></i><span :class="[$style.hisRight]" @click='clearHistory'>??????????????????</span></div>
            <ul :class="$style.searchUl" v-show='searchHistory.length > 0'>
              <li v-for="(item, index) in searchHistory" :class='[highlightIndex == index ? $style.current: "", $style.searchSort]'>
                <a :href="`/search.html?q=${item}`" target="_blank">{{item}}</a>
              </li>
            </ul>
            <div :class="$style.hotTip">????????????</div>
            <ul>
              <li v-for="(item, index) in hotList" :class='[highlightIndex == (index + searchHistory.length) ? $style.current: "", $style.hotSort]'>
                <span :class="[$style.sort, $style[`top${index}`]]">{{index+1}}</span>
                <a :class="$style.sortTitle" :href="`/search.html?q=${item}`" target="_blank">{{item}}</a>
              </li>
            </ul>
          </div>
          <div :class="[$style.searchTip, $style.suggestionTip]" v-show='focused && searchStatus == 1 && keyword !== null && keyword.trim().length > 0 && suggestions !== null && suggestions.length !== 0'>
            <ul>
              <li v-for="(item, index) in suggestions" :class='[matchedIndex == index ? $style.current: "", $style.matchSort]'>
                <a :href="`/search.html?q=${item.videoName}`" target="_blank">{{item.videoName}}</a>
              </li>
            </ul>
          </div>
          <button type='submit' :class="[$style.searchBtn, uiType == 'player' ? $style.bgDark: '']">
            <i :class="[$style.searchIcon, 'iconfont', 'icon-search', uiType == 'player' ? $style.colorGray: '']"></i>
          </button>
        </form>
      </div>
    </div>
    <div :class="$style.items">
      <div :class="$style.download">
        <i :class="[$style.downloadFont, 'iconfont', 'icon-download']"></i>
        <span>??????</span>
        <div :class="$style.triangle"></div>
        <div :class="$style.qrcodeContainer">
          <p :class="$style.fontBold">???????????????????????????</p>
          <p :class="$style.fontBold">????????????app</p>
          <img src="../../assets/header/qrcode.svg" :class="$style.qrcode">
          <p :class="$style.fontRegular">????????????</p>
        </div>
      </div>
      <div :class="$style.history">
        <i :class="[$style.historyFont, 'iconfont', 'icon-history']"></i>
        <span>??????</span>
        <div :class="$style.triangle"></div>
        <div :class="$style.historyContainer">
          <ul v-if='watchHistory && watchHistory.length > 0'>
            <li class="u-clearfix" v-for="(item, index) in watchHistory">
              <a :href="`/play.html?videoId=${item.videoId}#${item.videoDetailId}`" target="_blank">
                <i :class="[$style.active, 'iconfont', 'icon-play2']"></i>
                <span :class="$style.historyName">{{item.name}}</span>
                <span :class="$style.historyRecord">{{item.progress}}</span>
              </a>
            </li>
          </ul>
          <div v-else :class="[$style.noHistory]">
            <div :class="[$style.noHistoryLogo]">
              <i :class="['iconfont', 'icon-nohistory']"></i>
            </div>
            <span>?????????????????????????????????????????????~</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</header>
</template>

<script>
import { throttle, storage, debounce } from '../../utils'
import commonMixins from '@/mixins/common'

export default {
  name: 'e-header',

  mixins: [commonMixins],

  props: {
    uiType: {
      type: String,
      default: 'common'
    },
    pageType: {
      type: String,
      default: 'common'
    }
  },

  data() {
    return {
      searchStatus: 0, // 0??????????????????1??????????????????
      currentIndex: undefined,
      highlightIndex: undefined,
      matchedIndex: undefined,
      hots: [],
      channels: [],
      suggestions: [],
      schemeUrl: '',
      url: '',
      videoName: '',
      keyword: '',
      fadeIn: false,
      hide: false,
      fadeOut: false,
      showChannel: false,
      focused: false,
      watchHistory: [],
      searchHistory: storage.get('searchHistory') ? storage.get('searchHistory').reverse().splice(0, 5) : []
    }
  },

  watch: {
    keyword(val) {
      if (val !== null && val.trim().length !== 0) {
        this.url = val.trim()
      } else {
        this.searchStatus = 0
        this.currentIndex = undefined
        this.matchedIndex = undefined
        this.url = this.videoName
      }
    },
    currentIndex(currentIndex) {
      if (this.searchStatus === 0) {
        const leftIndex = currentIndex % 10
        const searchHistory = this.searchHistory
        let index
        if (currentIndex < 0) {
          index = leftIndex === 0 ? 0 : leftIndex + 10
        } else {
          index = leftIndex
        }

        if (this.searchStatus === 0 && !isNaN(index) && index >= searchHistory.length) {
          this.keyword = this.hotList[index - searchHistory.length]
        } else if (this.searchStatus === 0 && !isNaN(index)) {
          this.keyword = searchHistory[index]
        }

        this.highlightIndex = index
      } else {
        let index
        const suggestionLength = this.suggestions.length
        const leftIndex = currentIndex % suggestionLength

        if (currentIndex < 0) {
          index = leftIndex === 0 ? 0 : leftIndex + suggestionLength
        } else {
          index = leftIndex
        }
        if (this.searchStatus === 1 && !isNaN(index)) {
          this.keyword = this.suggestions[index].videoName
        }
        this.matchedIndex = index
      }
    }
  },

  computed: {
    hotList() {
      const list = []
      if (this.hots.length === 0) {
        return list
      }
      let index = 0
      while (list.length < (10 - this.searchHistory.length)) {
        list.push(this.hots[index].videoName)
        index++
      }

      return list
    }
  },

  methods: {
    calcScroll() {
      const scrollTop = document.body.scrollTop || document.documentElement.scrollTop

      if (scrollTop > 371 && scrollTop <= 570) {
        this.hide = true
        this.fadeIn = false
        this.showChannel = false
        if (this.pageType === 'player' && this.uiType === 'common') {
          this.$emit('typeChange', 'player')
        }
      } else if (scrollTop > 570) {
        this.hide = false
        this.fadeIn = true
        this.showChannel = true
        if (this.pageType === 'player' && this.uiType === 'player') {
          this.$emit('typeChange', 'common')
        }
      } else {
        this.showChannel = false
        this.hide = false
        this.fadeIn = false
        if (this.pageType === 'player' && this.uiType === 'common') {
          this.$emit('typeChange', 'player')
        }
      }
    },
    getWatchHistory() {
      const history = []
      const origin = storage.get('watchHistory')
      if (Array.isArray(origin)) {
        origin.forEach((video, index) => {
          let name
          if (video.categories === 2) {
            if (video.bizType === 1) {
              // ?????????
              if (video.name.length > 10) {
                name = `${video.name.substring(0, 10)}...???${video.episodes}???`
              } else {
                name = `${video.name}???${video.episodes}???`
              }
            } else {
              // ??????
              if (video.name.length > 8) {
                name = `${video.name.substring(0, 8)}...${video.episodes.replace(/\//g, '-')}`
              } else {
                name = `${video.name}${video.episodes.replace(/\//g, '-')}`
              }
            }
          } else {
            // ??????
            if (video.name.length > 14) {
              name = `${video.name.substring(0, 14)}...`
            } else {
              name = video.name
            }
          }
          history.push({
            name,
            progress: video.progress >= 100 ? '?????????' : `??????${video.progress || 0}%`,
            videoId: video.videoId,
            videoDetailId: video.videoDetailId
          })
        })
      }

      this.watchHistory = history
    },
    clearHistory() {
      storage.set('searchHistory', [])
      this.searchHistory = []
    },
    handleSearchBlur() {
      setTimeout(() => {
        this.currentIndex = undefined
        this.focused = false
      }, 200)
    },
    getSuggestion: debounce(function(event) {
      const word = event.target.value
      if (word.trim().length === 0) return
      this.$fetch('/web/search/suggest', { word, pageSize: 10 }).then(data => {
        this.currentIndex = undefined
        this.searchStatus = 1
        this.suggestions = data.data || []
      })
    }, 200),
    handleKeyDown(event) {
      const code = event.keyCode
      if (code === 40 || (event.metaKey && code === 74)) {
        event.preventDefault()
        if (this.currentIndex === undefined) {
          this.currentIndex = 0
        } else {
          this.currentIndex += 1
        }
      } else if ((code === 38) || (event.metaKey && code === 75)) {
        event.preventDefault()
        if (this.currentIndex === undefined) {
          this.currentIndex = 9
        } else {
          this.currentIndex -= 1
        }
      } else if (code === 27) {
        event.target.blur()
      }
    },
    getHots() {
      this.$fetch('/web/search/hotTop').then(data => {
        if (data.code === 0) {
          const hots = data.data || []
          this.hots = hots
          const length = hots.length
          const random = Math.floor(Math.random() * length)
          const hot = hots[random]

          if (hot !== undefined && hot !== null) {
            this.videoName = hot.videoName
            this.schemeUrl = hot.schemeUrl
            if (this.keyword !== null && this.keyword.trim() === '') {
              this.url = hot.videoName
            }
          }
          // this.url = hot.schemeUrl
        }
      })
    },
    getChannels() {
      this.$fetch('/web/ableChannel').then(data => {
        this.channels = data.data
      })
    }
  },

  created() {
    this.getHots()
    this.getChannels()
  },

  mounted() {
    this.getWatchHistory()
    if (document.addEventListener) {
      document.addEventListener('scroll', throttle(this.calcScroll, 100), false)
    } else {
      document.attachEvent('scroll', throttle(this.calcScroll, 100))
    }

    const keyword = this.getUrlParam('q')
    this.keyword = keyword || ''
  }
}
</script>

<style lang="postcss" module>
.nav.hide {
  opacity: 0;
}
.nav.fadeIn {
  position: fixed;
  opacity: 1;
  transition-property: opacity;
  transition-duration: 0.5s;
  transition-timing-function: ease;
}
.nav {
  position: absolute;
  top: 0;
  z-index: 10;
  width: 100%;
  background: #222222;
  color: #9A9A9A;
  height: 58px;
  .navContainer {
    height: 100%;
    width: 1150px;
    margin: 0 auto;
  }
  .logo {
    margin-right: 15px;
    margin-top: 9px;
    background-image: url('../../assets/header/logo.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    display: inline-block;
    width: 130px;
    height: 40px;
  }
  .channel {
    cursor: pointer;
    position: relative;
    line-height: 9px;
    vertical-align: top;
    height: 100%;
    display: inline-block;

    &:hover {
      color: #EDB734;
    }

    & > span {
      margin-top: 24px;
      font-weight: 400;
      font-size: 15px;
      display: inline-block;
      position: relative;
      top: 1px;
    }

    .highlight {
      color: #EDB734;
    }

    .channelIcon {
      font-size: 24px;
      display: inline-block;
      vertical-align: bottom;
    }

    .triangle {
      left: 20px;
      top: 50px;
      z-index: 10;
      display: none;
    }

    &:hover {
      .channelBox {
        display: block;
      }
      .triangle {
        display: block;
      }
    }

    .channelBox {
      display: none;
      color: #272727;
      font-size: 15px;
      position: absolute;
      left: -100px;
      z-index: 9;
      border-radius: 3px;
      top: 58px;
      background: #fff;
      width: 230px;
      padding: 10px 5px 10px 15px;
      border: 1px solid #DADADA;

      & > ul {
        width: 100%;
        margin: 0 auto;

        & > li {
          text-indent: 8px;
          width: 31%;
          height: 30px;
          line-height: 30px;
          display: inline-block;

          &:hover {
            color: #EDB734;
          }
        }
      }
    }
  }
  .searchContainer {
    margin-top: 14px;
    vertical-align:top;
    margin-left: 15px;
    display: inline-block;
    width: 500px;
    height: 32px;
  }
  .searchBoxContainer {
    position: relative;
    display: inline-block;
    float: left;

    form {
      width: 100%;
    }
  }
  .searchBox {
    font-family: PingFangSC-Regular;
    display: inline-block;
    width: 369px;
    height: 22px;
    line-height: 22px;
    float: left;
    outline: none;
    border-radius: 39px 0 0 39px;
    border: none;
    background-color: #E5E5E5;
    font-size: 14px;
    color: #5F5F5F;
    padding: 5px 20px;

    &::-webkit-input-placeholder {
      color: #9a9a9a;
    }

    &:focus::-webkit-input-placeholder {
      color: transparent;
    }

    &:focus::-moz-placeholder {
      color: transparent;
    }

    &:focus:-ms-input-placeholder {
      color: transparent;
    }

    &:focus {
      background-color: #FFFFFF;
    }
  }
  .searchBtn {
    border: none;
    background-color: #EFBA23;
    cursor: pointer;
    float: left;
    position: relative;
    width: 64px;
    padding: 0;
    height: 32px;
    display: inline-block;
    border-radius: 0 100px 100px 0;

    .searchIcon {
      position: absolute;
      top: 1px;
      left: 17px;
      font-size: 28px;
      color: #fff;
    }
  }
  .bgDark {
    background: #333333;
  }
  .bgGray {
    background: #444444 !important;
    color: #fff;
  }
  .colorGray {
    color: #9A9A9A !important;
    &:hover {
      color: #EFBA23 !important;
    }
  }
  .items {
    height: 100%;
    float: right;
    vertical-align: top;
    text-align: center;
    display: inline-block;
    font-size: 14px;
    line-height: 8px;
  }
  .qrcodeContainer {
    display: none;
    z-index: 9;
    width: 195px;
    height: 178px;
    padding: 15px 0;
    left: -75px;
    top: 58px;
    background: #fff;
    background-position: center -5px;
    border: 1px solid #DADADA;
    border-radius: 3px;
    position: absolute;

    .fontBold {
      line-height: 22px;
      color: #b8b8b8;
      font-size: 13px;
      font-weight: 400;
    }
    .fontRegular {
      line-height: 22px;
      color: #b8b8b8;
      font-size: 10px;
    }
  }
  .qrcode {
    margin-top: 10px;
    width: 102px;
    height: 102px;
  }
  .historyContainer {
    display: none;
    z-index: 9;
    background: #fff;
    position: absolute;
    width: 294px;
    border: 1px solid #DADADA;
    border-radius: 3px;
    left: -200px;
    top: 58px;

    .active {
      display: none;
      position: absolute;
      left: -20px;
      color: #f2bb3c;
      font-size: 20px;
      float: left;
    }

    a {
      margin: 0 auto;
      display: block;
      width: 85%;
      position: relative;
    }

    & > ul {
      padding: 10px 0;
      text-align: left;
      & > li {
        height: 33px;
        line-height: 33px;
        width: 100%;

        &:hover {
          background: #EAEAEA;

          .active {
            display: inline-block;
          }
        }
      }
    }

    .noHistory {
      position: relative;
      height: 250px;

      .noHistoryLogo {
        color: #b8b8b8;
        width: 100%;
        text-align: center;

        i {
          display: block;
          font-size: 100px;
          position: absolute;
          top: 90px;
          left: 50%;
          margin-left: -50px;
        }
      }

      span {
        position: absolute;
        top: 160px;
        color: #B8B8B8;
        display: block;
        width: 100%;
        height: 50px;
        text-align: center;
        font-size: 15px;
      }
    }
  }
  .historyName {
    width: 195px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    float: left;
    font-size: 13px;
    color: #000;
  }
  .historyRecord {
    float: left;
    font-size: 12px;
    color: #AFAFAF;
  }
  .download {
    position: relative;
    cursor: pointer;
    display: inline-block;
    height: 100%;

    &:hover {
      color: #f2bb3c;
    }

    & > span {
      margin-top: 26px;
      display: inline-block;
    }

    .triangle {
      display: none;
      z-index: 10;
      left: 30%;
      top: 50px;
    }

    &:hover {
      .triangle {
        display: block;
      }
      .qrcodeContainer {
        display: block;
      }
    }

    .downloadFont {
      font-size: 24px;
      vertical-align: bottom;
      display: inline-block;
    }
  }
  .history {
    position: relative;
    cursor: pointer;
    margin-left: 30px;
    display: inline-block;
    height: 100%;

    &:hover {
      color: #f2bb3c;
    }

    & > span {
      margin-top: 26px;
      display: inline-block;
    }

    &:hover {
      .triangle {
        display: block;
      }
      .historyContainer {
        display: block;
      }
    }

    .triangle {
      left: 30%;
      top: 50px;
      z-index: 10;
      display: none;
    }

    .historyFont {
      display: inline-block;
      vertical-align: bottom;
      font-size: 24px;
    }
  }
  .triangle {
    position: absolute;
    width: 18px;
    height: 9px;
    background-image: url('../../assets/header/triangle.png');
    background-size: contain;
    background-repeat: no-repeat;
  }
  .hotTip {
    margin-left: 20px;
    height: 20px;
    color: #C1C1C1;
    font-size: 14px;
  }
  .searchUl {
    margin-bottom: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #EBEBEB;
  }
  .searchTip {
    position: absolute;
    top: 31px;
    background: #fff;
    margin-left: 18px;
    width: 389px;
    border: 1px solid #DADADA;
    padding: 10px 0 10px;

    .current {
      background-color: #EAEAEA;
    }

    .hisTitle {
      height: 30px;
      font-size: 13px;
      position: relative;

      i {
        position: absolute;
        right: 100px;
        top: 4px;
        cursor: pointer;
      }
    }

    .hisLeft {
      top: 5px;
      position: absolute;
      left: 18px;
      font-size: 14px;
      color: #C1C1C1;
    }

    .hisRight {
      cursor: pointer;
      top: 5px;
      position: absolute;
      right: 18px;
      color: #999999;
    }

    .sort {
      position: absolute;
      top: 8px;
      left: 20px;
      height: 18px;
      width: 18px;
      line-height: 18px;
      text-align: center;
      color: #FFFFFF;
      font-size: 12px;
      display: block;
      background: #DCDCDC;

      &.top0 {
        background: #FABE00;
      }

      &.top1 {
        background: #FFD85B;
      }

      &.top2 {
        background: #FFE181;
      }
    }


    .hotSort {
      position: relative;
    }

    .sortTitle {
      position: absolute;
      top: 0;
      left: 50px;
    }

    &.suggestionTip {
      padding: 10px 0 10px;
    }

    .searchSort, .matchSort {
      text-indent: 20px;
    }

    li {
      width: 100%;
      cursor: pointer;
      font-size: 13px;
      height: 33px;
      line-height: 33px;
      color: #000;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      &:hover { background-color: #EAEAEA; }

      a {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
  }
}
</style>



