<template>
<div :class="$style.eLayoutTitle">
  <span :class="$style.eLayoutTitleName">{{ title }}</span>
  <a :class="$style.eLayoutTitleLink" :href="moreLink" v-if="moreLinkFlag">??????</a>
</div>
</template>

<script>
export default {
  name: 'e-layout-title',

  props: {
    title: String,
    moreLinkFlag: {
      type: Boolean,
      default: true,
    },
    moreLink: {
      type: String,
      default: '#'
    }
  },

  mounted () {
    // console.log(this.$style)
  }
}
</script>

<style module>
.e-layout-title {
  overflow: hidden;
  width: 100%;
  border-bottom: 1px solid #E5E5E5;
  transform: translateY(3px);
  &__name {
    float: left;
    height: 24px;
    line-height: 24px;
    padding-left: 8px;
    margin-bottom: 10px;
    font-size: 24px;
    /*font-weight: bold;*/
    border-left: 4px solid #FABE00;
    overflow: hidden;
    white-space: nowrap;
    max-width: 481px;
  }
  &__link {
    float: right;
    font-size: 14px;
    line-height: 34px;
    color: #666;
    &:hover {
      color: #FABE00;
    }
  }
}
</style>



