import ua from './ua.js'

let point = null
let el
let addListener = null
let removeListener = null
let padding = 0 // ?????????????????????????????????????????? ?????????????????????????????????
let viewWidth = Math.max(document.documentElement.clientWidth, window.innerWidth || 0) // viewport width
let viewHeight = Math.max(document.documentElement.clientHeight, window.innerHeight || 0) // viewport height
let eleWidth = 0 // element width
let eleHeight = 0 // element height
let defaultOverflow = ''

if (document.addEventListener) {
  addListener = function(el, type, listener) {
    el.addEventListener(type, listener, false)
  }
  removeListener = function(el, type, listener) {
    el.removeEventListener(type, listener, false)
  }
} else {
  addListener = function(el, type, listener) {
    el.attachEvent(type, listener)
  }
  removeListener = function(el, type, listener) {
    el.detachEvent(type, listener)
  }
}

function mousedown(e) {
  e.preventDefault()
  el = this
  const rect = el.getBoundingClientRect()
  point = { x: e.clientX - rect.left, y: e.clientY - rect.top }

  if (ua.mac) {
    defaultOverflow = document.body.style.overflow
    document.body.style.overflow = 'hidden' // disable document scrolling to prevent mini-player being dragged out of current view scope.
  }
  disableScroll()

  addListener(document, 'mouseup', mouseup)

  addListener(document, 'mousemove', mousemove)

  return false
}

function mouseup(e) {
  e.preventDefault()
  if (ua.mac) {
    document.body.style.overflow = defaultOverflow // restore to default overflow
  }
  setXPos(e)
  setYPos(e)
  point = null
  enableScroll()
  removeListener(document, 'mousemove', mousemove)
  removeListener(document, 'mouseup', mouseup)

  return false
}

function mousemove(e) {
  e.preventDefault()
  if (point) {
    setXPos(e)
    setYPos(e)
  }

  return false
}

function setXPos(e) {
  let deltaX = e.clientX - point.x
  if (deltaX >= padding && deltaX <= viewWidth - eleWidth - padding) {
    el.style.left = `${deltaX}px`
  } else if (deltaX < padding) { // ???????????????
    el.style.left = padding + 'px'
  } else if (deltaX > viewWidth - eleWidth - padding) { // ???????????????
    el.style.left = viewWidth - eleWidth - padding + 'px'
  }
}

function setYPos(e) {
  let deltaY = e.clientY - point.y
  if (deltaY >= padding && deltaY <= viewHeight - eleHeight - padding) {
    el.style.top = `${deltaY}px`
  } else if (deltaY < padding) { // ???????????????
    el.style.top = padding + 'px'
  } else if (deltaY > viewHeight - eleHeight - padding) { // ???????????????
    el.style.top = viewHeight - eleHeight - padding + 'px'
  }
}

function onWinResize(el) {
  window.onresize = function() {
    viewWidth = Math.max(document.documentElement.clientWidth, window.innerWidth || 0) // viewport width
    viewHeight = Math.max(document.documentElement.clientHeight, window.innerHeight || 0) // viewport height
  }
}
// ??????mousescroll??????
function preventDefault(e) {
  e = e || window.event
  if (e.preventDefault) {
    e.preventDefault()
  }
  e.returnValue = false
}

function disableScroll() {
  if (window.addEventListener) { // older FF
    window.addEventListener('DOMMouseScroll', preventDefault, false)
  }
  window.onwheel = preventDefault // modern standard
  window.onmousewheel = document.onmousewheel = preventDefault // older browsers, IE
  window.ontouchmove = preventDefault // mobile
}

function enableScroll() {
  if (window.removeEventListener) {
    window.removeEventListener('DOMMouseScroll', preventDefault, false)
  }
  window.onmousewheel = document.onmousewheel = null
  window.onwheel = null
  window.ontouchmove = null
  document.onkeydown = null
}

export default function(el, type) {
  if (type) {
    el.style.cursor = 'move'
    eleWidth = el.offsetWidth
    eleHeight = el.offsetHeight
    onWinResize(el)
    addListener(el, 'mousedown', mousedown)
  } else {
    el.style.cursor = 'default'
    removeListener(el, 'mousedown', mousedown)
  }
}



