<template>
<div :class="$style.navigation">
  <ul>
    <li :class="value === option.id ? $style.active : ''" v-for="option in channels" :style="`${navItemStyle}`">
      <a href="javascript:void(0)" :style="`${navLinkStyle}`" @click="changeChannel(option.id)" v-if="navType === 0">{{option.name}}</a>
      <a :href="`/?channelId=${option.id}`" :style="`${navLinkStyle}`" v-else>{{option.name}}</a>
    </li>
  </ul>
</div>
</template>

<script>
export default {
  name: 'e-navigation',

  props: {
    value: Number,
    navType: {
      type: Number,
      default: 1,
    }, // ???????????????????????? 0:?????????1:??????
    navItemStyle: String,
    navLinkStyle: String
  },

  data () {
    return {
      channels: [],
    }
  },

  methods: {
    async fetchData() {
      let res = await this.$fetch('/web/ableChannel')
      this.channels = res.data
    },
    changeChannel(id) {
      this.$emit('click', id)
    },
  },

  created () {
    this.fetchData()
  }
}
</script>

<style lang="postcss" module>
.navigation {
  overflow: hidden;
  width: 1200px;
  height: 40px;
  line-height: 37px;
  color: #272727;
  font-size: 16px;
  margin: 58px auto 0 auto;
  ul li {
    float: left;
    margin: 0 20px;
    padding: 0 5px;
    &:hover {
      color: #FABE00;
    }
    a {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  .active {
    color: #FABE00;
    border-bottom: 3px solid #FABE00;
  }
}
</style>



