<template>
  <div :class="$style.typeContainer">
    <div :class="$style.inner" v-if="isShow === true">
      <div :class="$style.itemContainer">
        <div v-for="(option, index) in options" v-if="itemsShowType[index] === 1">
          <ul :class="$style.list">
            <li :class="[$style.title]">{{option.groupName}}:</li>
            <li @click="clickItem(option.id, -1)" :class="[$style.name, isAllActive(option.id, option.labels) ? $style.allActive : '']"><span :class="$style['txt']">??????</span></li>
            <li v-for="item in option.labels
          " @click="clickItem(option.id, item.id)" :class="[$style.name, setActive(option.id, item.id) === true ? $style.active : '']">{{item.labelName}}</li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import commonMixins from '@/mixins/common'
export default {
  name: 'e-filter',
  mixins: [commonMixins],
  data() {
    return {
      id: 0,
      subId: 0,
      labels: '',
      groups: {},
      options: [],
      isShow: false,
      itemsShowType: [],
      activeItemId: '',
      activeItemName: '',
      url: ''
    }
  },
  created() {
    this.fetchFilters()
    this.labels = this.getUrlParam('labels')
    this.groups = JSON.parse(this.labels)
  },
  computed: {
  },
  methods: {
    isAllActive(id, labels) { // ??????????????????????????????????????????????????? ?????????
      console.log(id, labels)
      let len = labels.length
      for (let i = 0; i < len; i++) {
        if (this.setActive(id, labels[i].id)) {
          return false
        }
      }
      return true
    },
    async fetchFilters() {
      let res = await this.$fetch('/web/search/allGroupLabels')
      if (res.code === 0) {
        if (res.data && res.data.length > 0) {
          this.resData = res.data
          this.getChannels(res.data) // ??????
        } else {
          this.isShow = false
        }
      } else {
        this.isShow = false
      }
    },
    clickItem(id, subId) {
      this.id = id
      this.subId = subId
      let groupsData = JSON.parse(this.getUrlParam('labels'))
      const groupLength = groupsData.groups.length
      let count = 0
      let isDelete = false
      let deleteGroupId = 0
      let deleteIndex = 0
      // ?????????????????????????????????
      for (let i = 0; i < groupLength; i += 1) {
        if (this.id !== groupsData.groups[i].groupId && this.id.toString() !== groupsData.groups[i].groupId) {
          count += 1
        } else {
          if (this.subId === -1) {
            isDelete = true
            deleteGroupId = id
          } else {
            groupsData.groups[i].labels = [this.subId]
          }
        }
      }
      for (let i = 0; i < groupLength; i += 1) {
        if (deleteGroupId === groupsData.groups[i].groupId || deleteGroupId.toString() === groupsData.groups[i].groupId) {
          deleteIndex = i
        }
      }
      if (isDelete) {
        groupsData.groups.splice(deleteIndex, 1)
      }
      if (count === groupLength) {
        const addArr = {
          groupId: this.id,
          labels: [this.subId]
        }
        groupsData.groups.push(addArr)
      }
      if (groupsData.groups.length > 0) {
        for (let i = 0; i < groupsData.groups.length; i += 1) {
          if (groupsData.groups[i].labels && groupsData.groups[i].labels[0] === -1) {
            groupsData.groups.splice(i, 1)
          }
        }
        // if (groupsData.groups[0].labels && groupsData.groups[0].labels[0] === -1) {
        //   groupsData.groups = []
        // }
      }
      this.groups = groupsData
      this.labels = JSON.stringify(groupsData)
      location.search = '?go=videoList&labels=' + this.labels
    },
    setAllActive(id) {
      let isActive = true
      const labels = this.getUrlParam('labels')
      const groupsData = JSON.parse(labels)
      for (let i = 0; i < groupsData.groups.length; i += 1) {
        if (id === groupsData.groups[i].groupId || id.toString() === groupsData.groups[i].groupId) {
          if (groupsData.groups[i].labels[0] === '' || groupsData.groups[i].labels.length === 0) {
            isActive = true
          } else {
            isActive = false
          }
        }
      }
      return isActive
    },
    setActive(id, subId) {
      let isActive = false
      const labels = this.getUrlParam('labels')
      const groupsData = JSON.parse(labels)
      for (let i = 0; i < groupsData.groups.length; i += 1) {
        if ((id === groupsData.groups[i].groupId || id.toString() === groupsData.groups[i].groupId) && (subId === groupsData.groups[i].labels[0] || subId.toString() === groupsData.groups[i].labels[0])) {
          isActive = true
        }
      }
      return isActive
    },
    getChannels(data) {
      this.options = data
      let isShowCount = 0
      for (let i = 0; i < data.length; i += 1) {
        const labels = this.options[i].labels
        if (labels && labels.length > 0) {
          isShowCount += 1
        }
      }
      if (isShowCount > 0) {
        this.isShow = true
      } else {
        this.isShow = false
      }
      this.labels = this.getUrlParam('labels')
      this.groups = JSON.parse(this.labels)
      this.isItemsShow(this.options) // ??????????????????????????? ture?????????false??????
    },
    isItemsShow(data) {
      const length = data.length
      for (let i = 0; i < length; i += 1) {
        const result = (data[i].labels && data[i].labels.length > 0) ? 1 : -1
        this.itemsShowType.push(result)
      }
    }
  }
}
</script>

<style lang="postcss" module>
.typeContainer {
  font-family: PingFangSC-Regular;
  font-size: 13px;
  color: #4a4a4a;
  width: 1150px;
  margin: 73px auto 0;
  padding: 15px 0;
  .inner {
    background: #efefef;
    border-radius: 3px;
    .itemContainer {
      padding: 20px 16px;
      .list {
        display: inline-block;
        .name, .title {
          margin-left: 26px;
          text-align: left;
          display: inline-block;
          margin: 9px;
          text-align: left;
        }
        .title {
          color: #b4b4b4;
        }
        .name {
          cursor: pointer;
          &:hover {
            color: #EFBA23;
          }
          &.active, &.allActive {
            color: #FFFFFF;
            text-align: center;
            width: 44px;
            padding: 4px 0;
            margin: 4px 0;
            background: #FABE00;
            border-radius: 100px;
          }
        }
      }
    }
  }
}
</style>



