<template>
<div
  :class="$style.eLayout"
  :style="{
    width: `${width}px`,
    marginTop: `${top}px`
  }"
>
  <slot name="title"></slot>
  <ul
    :class="$style.eLayoutWrap"
  >
    <slot name="item"></slot>
  </ul>
</div>
</template>

<script>
import layoutTitle from './layout-title.vue'
import layoutItem from './layout-item.vue'

export default {
  name: 'e-layout',

  components: {
    'layout-title': layoutTitle,
    'layout-item': layoutItem
  },

  props: {
    width: {
      type: Number,
      default: 1150
    },
    top: {
      type: Number,
      default: 36
    },
    layoutId: {
      type: Number,
      default: 1
    },
  },
}
</script>

<style module>
.e-layout {
  margin: 0 auto;
  &__wrap {
   overflow: hidden;
  }
}
</style>



