<template>
  <li :class="[$style['grid-item'],active ? $style.active : '']">
    <a href="javascript:void(0);" @click="goToVideo">{{ label }}</a>
  </li>
</template>

<script>
export default {
  name: 'grid-item',
  props: {
    item: {
      type: Object,
      default: () => {}
    },
    label: {
      type: String
    },
    active: {
      type: Boolean,
      default: false
    },
    idxInVideoList: {
      type: Number
    }
  },
  data() {
    return {
    }
  },
  created() {
  },
  mounted() {
  },
  computed: {
  },
  methods: {
    goToVideo() {
      let videoId = this.item.videoBaseId
      let videoDetailId = this.item.videoDetailId
      this.$emit('goToVideo', videoId, videoDetailId, this.idxInVideoList)
    }
  }
}
</script>

<style lang="postcss" module>
.grid-item {
  float: left;
  background-color: #272727;
  /* font */
  font-family: PingFangSC-Medium;
  font-size: 17px;
  color: #9A9A9A;
  border: 1px solid #161616;
  & > a {
    text-align: center;
    display: block;
    height: 48px;
    width: 48px;
    font-weight: normal;
    line-height: 44px;
    border: 2px solid #272727;
    box-sizing: border-box;
    &:hover {
      border: 2px solid #FABE00;
    }
  }
  &.active { 
    color: #FABE00;
  }
}
</style>



