<template>
<li
  :class="$style.eLayoutItem"
  :style="{
    width: `${width}px`,
    marginTop: `${marginTop}px`
  }"
>
  <a
    class="u-bg--default"
    :href="`play.html?videoId=${item.videoBaseId}`" :class="$style.eLayoutItemLink" target="_blank"
    :style="{
      height: `${height}px`,
    }"
    @click="addSearchHot(item.videoBaseId)"
  >
    <img
      :class="$style.eLayoutItemImg"
      v-lazy="adaptImgUrl(getImgUrl(item.images), width, height)"
      alt=""
    >
    <span
      v-if="item.extMap.sign"
      :class="[$style.eLayoutItemSub, $style.eLayoutItemSubrt]"
    >
      <img :src="signIcon(item.extMap.sign)" alt="">
    </span>
    <span
      v-if="item.extMap.updateEpisode"
      :class="[$style.eLayoutItemSub, $style.eLayoutItemSubrb]"
    >
    {{ item.extMap.updateEpisode }}
    </span>
    <span
      v-if="item.extMap.clarity"
      :class="[$style.eLayoutItemSub, $style.eLayoutItemSublb]"
    >
    {{ item.extMap.clarity }}
    </span>
    <div
      class="iconfont icon-play1"
      :class="$style.eLayoutItemMask"
      :style="{
        fontSize: `${iconSize}px`,
        height: `${height}px`,
        paddingTop: `${(height - iconSize) / 2}px`
      }"
    >
    </div>
  </a>
  <div :class="$style.eLayoutItemTitle">
    <a :href="`play.html?videoId=${item.videoBaseId}`" target="_blank" @click="addSearchHot(item.videoBaseId)" v-html="setHighLight(item.name)"></a>
  </div>
  <div :class="$style.eLayoutItemCaption" v-if="isSubTitleShow" v-html="setHighLight(item.subName)"></div>
</li>
</template>

<script>
import commonMixins from '@/mixins/common'

export default {
  name: 'e-layout-item',

  mixins: [commonMixins],

  data () {
    return {
      // 1-16:9 2-4:3 3-2:3 4-3:2
      defaultImg: require('../../assets/common/none.png')
    }
  },

  props: {
    item: {},
    width: {
      type: Number,
      default: 214
    },
    marginTop: {
      type: Number
    },
    height: {
      type: Number,
      default: 321
    },
    iconSize: {
      type: Number,
      default: 64
    },
    layout: {
      type: Number,
      default: 1
    },
    searchHotFlag: {
      type: Boolean,
      default: false
    },
    isSubTitleShow: {
      type: Boolean,
      default: true
    },
    highLightWord: {
      type: String,
      default: ''
    },
  },

  methods: {
    getImgUrl(images) {
      let ret, arr
      switch (this.layout) {
        case 1:
          arr = images.filter(image => image.scale === 3)
          break
        case 2:
          arr = images.filter(image => image.scale === 1)
          break
        case 3:
          arr = images.filter(image => image.scale === 3)
          break
        default:
          break
      }
      ret = arr[0] ? this.prefixImgUrl(arr[0].url) : this.defaultImg
      return ret
    },
    // setVideoLink(id) {
    //   return this.searchHotFlag ? 'play.html?videoId=' + id + '&source=search' : 'play.html?videoId=' + id
    // },
    setHighLight(word) {
      if (this.searchHotFlag) {
        const wordList = this.highLightWord.split(' ')
        let wordStr = wordList.join('|')
        wordStr = this.escape(wordStr)
        return word.replace(new RegExp(wordStr, 'g'), (res) => {
          return '<font>' + res + '</font>'
        })
      }
      return word
    },
    escape(word) {
      return word.replace(/[-\/\\^$*+?.()|[\]{}]/g, '\\$&')
    },
    signIcon(sign) {
      const signMap = {
        '??????': require('../../assets/common/only-play.svg'),
        '??????': require('../../assets/common/self-made.svg'),
      }
      return signMap[sign]
    },
    addSearchHot(id) {
      this.$emit('click', id)
    },
  },
}
</script>

<style module>
.e-layout-item {
  overflow: hidden;
  float: left;
  margin-top: 17px;
  &__link {
    position: relative;
    float: left;
    overflow: hidden;
    width: 100%;
    cursor: pointer;
    &:hover {
      :global(.iconfont) {
        display: block;
      }
    }
  }
  &__img {
    float: left;
    width: 100%;
    height: 100%;
  }
  &__sub {
    position: absolute;
    display: block;
    padding: 4px;
    background: rgba(0, 0, 0, 0.7);
    border-radius: 3px;
    color: #fff;
    font-size: 12px;
    &rt {
      right: 0;
      top: 0;
      padding: 0;
      background: none;
    }
    &rb {
      right: 5px;
      bottom: 5px;
    }
    &lb {
      left: 5px;
      bottom: 5px;
    }
  }
  &__mask {
    display: none;
    position: absolute;
    width: 100%;
    background: rgba(0, 0, 0, 0.4);
    color: #F3BC29;
    text-align: center;
  }
  &__title {
    float: left;
    width: 100%;
    line-height: 21px;
    margin-top: 4px;
    font-size: 15px;
    /*font-weight: bold;*/
    color: #292929;
    overflow: hidden;
    /*text-overflow: ellipsis;*/
    white-space: nowrap;
    &:hover {
      color: #FABE00;
    }
    font {
      color: #FABE00;
    }
  }
  &__caption {
    float: left;
    width: 100%;
    line-height: 17px;
    margin-top: 1px;
    font-size: 12px;
    color: #9D9D9D;
    overflow: hidden;
    /*text-overflow: ellipsis;*/
    white-space: nowrap;
    font {
      color: #FABE00;
    }
  }
}
</style>



