<template>
  <ul :class="['u-clearfix',$style['tab-row']]">
    <li v-for="(item, index) in tabs" @click="switchToTab(index)">
      <a :class="item.active ? $style['active'] : ''" href="javascript:void(0);">{{ item.label }}</a>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'tab-row',
  props: {
    tabs: {
      type: Array,
      default: () => []
    },
  },
  data () {
    return {
    }
  },
  ready () {
  },
  mounted () {
  },
  computed: {
  },
  methods: {
    switchToTab(index) {
      this.$emit('click', index)
    }
  }
}
</script>

<style lang="postcss" module>
.tab-row {
  margin-left: 19px !important;
  width: 251px;
  border-bottom: 1px solid #272727;;
  & > li { 
    float: left;
    margin-right: 20px;
    font-family: PingFangSC-Medium;
    font-size: 15px;
    color: #4B4B4B;
    & > a {
      display: block;
      padding-bottom: 4px;
      &.active {
        color: #9A9A9A;
        border-bottom: 2px solid #FABE00;
      }
    }
  }
}
</style>



