<template>
<div id='app'>
  <e-header></e-header>
  <e-filter></e-filter>
	<e-layout :top="5" v-if="searchResCurrCount !== 0 && searchApiFlag">
		<e-layout-item
      slot="item"
      v-for="(item, index) in searchRes"
      :key="index"
      :item="item"
      :isSubTitleShow=false
      :style="{ marginRight: `${(index + 1) % 5 === 0 ? '0px' : '20px' }` }"
    ></e-layout-item>
	</e-layout>
  <div :class="$style.loadingData" v-if="!searchApiFlag">
    <img src="../../assets/common/card-loading.gif" alt="">
  </div>
  <div :class="$style.noResArea" v-if="searchResCurrCount === 0 && searchApiFlag">
    <img :class="$style.noResIcon" src="../../assets/common/none.png" alt="">
    <p :class="$style.noResContent">?????????????????????????????????</p>
    <div :class="$style.hotwordArea">
      <p :class="$style.hotwordTitle">???????????????????????????</p>
      <div :class="$style.hotwordBox">
        <ul>
          <li v-for="item in hotWord">
            <a :href="`/search.html?q=${item.videoName}`">{{item.videoName}}</a>
          </li>
        </ul>
      </div>
    </div>
  </div>
  <e-paging :page-count="pageCount" @page="handlePage" :currentPage="currentPage" v-if="pageCount > 1"></e-paging>
  <e-footer></e-footer>
  <e-back-top></e-back-top>
</div>
</template>

<script>
import commonMixins from '@/mixins/common'
export default {
  mixins: [commonMixins],
  data() {
    return {
      labels: '',
      groups: {},
      pageSize: 20,
      currentPage: 1,
      searchRes: [],
      searchResCount: 0, // ????????????
      searchResCurrCount: 0, // ?????????????????????
      hotWord: [],
      searchApiFlag: false,
    }
  },
  created() {
    this.currentPage = parseInt(this.getUrlParam('page')) || 1
    this.labels = this.getUrlParam('labels')
    this.groups = JSON.parse(this.labels)
    this.fetchData(this.currentPage, this.groups)
  },
  methods: {
    getUrl() {
      return '?go=videoList&labels={"groups": [{"groupId": 1,"labels": [14]}]}'
    },
    handlePage(page) {
      location.search = '?go=videoList&labels=' + this.labels + '&page=' + page
    },
    async fetchData(currentPage, groupsData) {
      let res = await this.$fetch('/web/search/selectQuery', {
        groups: groupsData.groups,
        pageSize: this.pageSize,
        currentPage: currentPage
      })
      if (res.code === 0) {
        this.searchRes = res.data.videos
        this.searchResCurrCount = res.data.videos.length
        this.searchResCount = res.data.records
      }
      this.searchApiFlag = true
      if (this.searchResCurrCount === 0) {
        let res = await this.$fetch('/web/search/hotTop')
        if (res.code === 0) {
          this.hotWord = res.data
        }
      }
    }
  },
  computed: {
    pageCount() {
      return Math.ceil(this.searchResCount / this.pageSize)
    }
  }
}
</script>

<style lang='postcss' module>
.loadingData {
  width: 200px;
  height: 200px;
  margin: 50px auto 200px auto;
  img {
    width: 100%;
    height: 100%;
  }
}
.noResArea {
  width: 1150px;
  margin: 0 auto;
  .noResIcon {
    display: block;
    margin: 80px auto 0 auto;
  }
  .noResContent {
    width: 300px;
    margin: 0 auto 96px auto;
    text-align: center;
    font-size: 20px;
    color: #B8B8B8;
  }
  .hotwordArea {
    .hotwordTitle {
      font-size: 20px;
      color: #B7B7B7;
    }
    .hotwordBox {
      overflow: hidden;
      background: #FAFAFA;
      border-radius: 3px;
      min-height: 40px;
      padding: 10px 0;
      margin-top: 16px;
      li {
        width: 230px;
        height: 20px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        line-height: 16px;
        float: left;
        margin: 10px auto;
        padding-left: 24px;
        box-sizing: border-box;
        a:hover {
          color: #FABE00;
        }
        &::before {
          content: ".";
          color: #FABE00;
          font-weight: bold;
          font-size: 20px;
          position: relative;
          top: -3px;
          margin-right: 10px;
        }
      }
    }
  }
}
</style>



