<template>
<div :class="$style.ePaging">
  <div :class="$style.ePagingWrap">
    <div
      v-show="currentPage !== 1"
      :class="prevNextClass(currentPage === 1, true)"
      @click="handlePaging(currentPage - 1)"
    >
      <i class="iconfont icon-toprev"></i>
    </div>
    <div
      v-for="i in pages"
      :class="pageClass(i)"
      @click="handlePaging(i)"
    >
      <span>{{ i }}</span>
    </div>
    <div
      v-show="currentPage !== pageCount"
      :class="prevNextClass(currentPage === pageCount, true)"
      @click="handlePaging(currentPage + 1)
    ">
      <i class="iconfont icon-tonext"></i>
    </div>
  </div>
</div>
</template>

<script>
import { fastArr } from '@/utils/array.js'

export default {
  name: 'e-paging',

  data() {
    return {
      pages: [1]
    }
  },

  props: {
    pageCount: { // ?????????
      type: Number,
      default: 1,
    },
    showCount: { // ??????????????????
      type: Number,
      default: 9,
    },
    currentPage: {
      type: Number,
      default: 1,
    }
  },

  watch: {
    currentPage(page) {
      this.pages = this.generatePages(page)
    }
  },

  methods: {
    pageClass(page) {
      let ret = []
      if (typeof page !== 'number') {
        ret.push(this.$style.ePagingEllipsis)
      } else {
        ret.push(this.$style.ePagingPage)
        if (this.currentPage === page) ret.push(this.$style.isActive)
      }
      return ret
    },
    prevNextClass(disable, prev) {
      let ret = [this.$style.ePagingPage]
      if (disable) {
        ret.push(this.$style.isDisabled)
      }
      if (prev) {
        ret.push(this.$style.ePagingPrev)
      } else {
        ret.push(this.$style.ePagingNext)
      }
      return ret
    },
    generatePages(page) {
      let { pageCount, showCount } = this
      let middle = showCount - 4 // 5
      if (pageCount <= showCount && middle > 0) { // ?????????????????????????????????
        return fastArr(pageCount)
      } else if (page < middle) { // ??????next??????
        return fastArr(showCount - 2).concat(['...', pageCount])
      } else if (page > pageCount - middle + 1) { // ??????prev??????
        return [1, '...'].concat(fastArr(showCount - 2, pageCount - middle - 1))
      } else {
        return [1, '...'].concat(fastArr(showCount - 4, page - 2)).concat(['...', pageCount])
      }
    },
    handlePaging(page) {
      if (typeof page !== 'number') return // ...????????????
      if (this.currentPage === page || page === 0 || page === this.pageCount + 1) return
      this.$emit('page', page)
    },
  },

  created () {
    this.pages = this.generatePages(this.currentPage)
  },
}
</script>

<style module>
.e-paging {
  display: inline-block;
  padding-bottom: 50px;
  margin-top: 50px;
  margin-left: 50%;
  transform: translateX(-50%);

  &__wrap {
    overflow: hidden;
    & > div {
      float: left;
    }
  }
  &__page {
    overflow: hidden;
    box-sizing: border-box;
    width: 40px;
    height: 40px;
    line-height: 40px;
    margin-left: 8px;
    text-align: center;
    color: #808080;
    font-size: 16px;
    background: #fff;
    border: 1px solid #E8E8E8;
    border-radius: 3px;
    &.is-active {
      color: #fff;
      background: #A3A3A3;
      border-color: #A3A3A3;
    }
    &.is-disabled {
      color: #E8E8E8;
      &:hover {
        color: #E8E8E8;
        background: #fff;
        border-color: #E8E8E8;
        cursor: disabled;
      }
    }
    &:hover {
      color: #fff;
      background: #FFC31B;
      border-color: #FFC31B;
      cursor: pointer;
    }
    & > i {
      display: inline-block;
      width: 100%;
      height: 100%;
    }
  }
  &__ellipsis {
    overflow: hidden;
    box-sizing: border-box;
    width: 40px;
    height: 40px;
    line-height: 40px;
    margin-left: 8px;
    text-align: center;
    color: #808080;
    font-size: 16px;
  }
}
</style>



