<template>
<li
  class="e-slide-item"
  :style="{
    width: `${width}px`,
    backgroundImage: `url(${adaptImgUrl(prefixImgUrl(item.imageUrl))})`,
    backgroundSize: 'cover'
  }"
  @click="handleClick"
>
</li>
</template>

<script>
import commonMixins from '@/mixins/common'

export default {
  name: 'e-slide-item',

  mixins: [commonMixins],

  props: {
    item: {},
    width: 0,
  },

  methods: {
    handleClick(e) {
      this.$emit('click', e)
    }
  }
}
</script>

<style>
.e-slide-item {
  float: left;
  cursor: pointer;
  height: 410px;
  background-color: #d8d8d8;
  background-image: url('../../assets/common/none.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 128px 128px;
}
</style>



