<template>
  <section :class="[$style['right-list-panel'],collapsed ? $style.collapsed : '', isFullPanel ? $style.fullpanel : '']">
    <h1 :class="$style['panel-name']">
      <span>????????????</span>
    </h1>
    <h2 :class="$style['series-name']">
      <a href="javascript:void(0);">{{ videoInfos.name }}</a>
    </h2>
    <!-- <h3 :class="$style['series-desc']">
      {{ videoInfos.subName }}
    </h3> -->
    <list-container
      :renderList="renderList"
      :selectedIndex="selectedIndex"
      :scrollbarHeight="scrollBarHeight"
      v-on:goToVideo="goToVideo"
    ></list-container>
    <a :class="$style['right-icon-wrapper']" href="javascript:void(0);" @click="toggleDrawer">
      <span class="iconfont" :class="[collapsed ? 'icon-left' : 'icon-right']"></span>
    </a>
  </section>
</template>

<script>
import listContainer from './list-container.vue'

export default {
  name: 'right-list-panel',
  components: {
    'list-container': listContainer
  },
  props: {
    videoInfos: {
      type: Object,
      required: true
    },
    isFullPanel: {
      type: Boolean,
      required: false
    },
    isPanelDisplay: {
      type: Boolean,
      required: false
    }
  },
  data () {
    return {
      collapsed: false,
      scrollBarHeight: null,
    }
  },
  created() {
  },
  mounted() {
  },
  watch: {
    isPanelDisplay(newValue, oldValue) {
      this.collapsed = !newValue
    },
    isFullPanel(newValue, oldValue) {
      if (newValue) {
        let viewHeight = Math.max(document.documentElement.clientHeight, window.innerHeight || 0) // viewport height
        let scrollBarHeight = viewHeight - 130
        this.scrollBarHeight = scrollBarHeight
      } else {
        this.scrollBarHeight = 398
      }
    }
  },
  computed: {
    renderList() {
      return this.videoInfos.vVideoDetailRspVos
    },
    selectedIndex() {
      return this.videoInfos.currentIndex
    }
  },
  methods: {
    goToVideo(videoId, videoDetailId, idxInVideoList) {
      this.$emit('goToVideo', videoId, videoDetailId, idxInVideoList)
    },
    toggleDrawer() {
      this.collapsed = !this.collapsed
      let width = this.collapsed ? 1140 : 870
      this.$emit('changeVideoWidth', width)
    }
  }
}
</script>

<style lang="postcss" module>
.right-list-panel {
  /* transition: width .2s ease; */
  width: 280px;
  height: 530px;
  background-color: #161616;
  position: absolute;
  top: 0;
  right: 0;
  overflow-x: hidden;
  &.fullpanel {
    opacity: 0.95;
    height: 100%;
    position: fixed;
    & > .right-icon-wrapper, & > .left-icon-wrapper {
      display: none;
    }
    &.collapsed {
      width: 0;
      background-color: black;
    }
  }
  & > .panel-name {
    width: 100%;
    font-family: PingFangSC-Medium;
    font-size: 16px;
    color: #FABE00;
    letter-spacing: -0.24px;
    height: 47px;
    line-height: 47px;
    border-bottom: 1px solid #272727;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-weight: normal;
    & > span {
      padding-left: 18px;
    }
  }
  & > .series-name {
    font-family: PingFangSC-Medium;
    font-size: 16px;
    color: #9A9A9A;
    letter-spacing: -0.24px;
    margin-left: 18px;
    margin-top: 16px;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-weight: normal;
    & > a {
      padding: 15px 0;
    }
  }
  & > .series-desc {
    font-family: PingFangSC-Medium;
    font-size: 12px;
    color: #9A9A9A;
    letter-spacing: -0.18px;
    position: absolute;
    top: 100px;
    left: 20px;
  }
  & > .right-icon-wrapper {
    display: block;
    position: absolute;
    background-color: #272727;
    width: 13px;
    height: 74px;
    line-height: 74px;
    left: 0;
    top: 228px;
    .icon-right,.icon-left{
      opacity: 0;
    }
  }
  &.collapsed {
    width: 13px;
    background-color: black;
  }
}
</style>



