<template>
  <li :class="[$style['list-item'],active ? $style.active : '','u-clearfix']">
    <a href="javascript:void(0);" @click="goToVideo" class="u-clearfix">
      <div :class="$style['img-wrapper']">
        <div :class="$style['img-border']"></div>
        <img v-lazy="adaptImgUrl(prefixImgUrl(imgUrl))" alt="">
        <span :class="$style['date']">{{ item.episodeDisplay }}</span>
      </div>
      <p :class="$style['intro']">
        {{ item.subTitle }}
      </p>
    </a>
  </li>
</template>

<script>
import commonMixins from '@/mixins/common'
export default {
  name: 'list-item',
  mixins: [commonMixins],
  props: {
    item: {
      type: Object,
      default: () => {}
    },
    active: {
      type: Boolean,
      default: false
    },
    idxInVideoList: {
      type: Number
    }
  },
  data () {
    return {
    }
  },
  ready () {
  },
  mounted () {
  },
  computed: {
    imgUrl() {
      if (!this.item.images[0]) return ''
      let imgUrl = this.item.images[0].url ? this.item.images[0].url : ''
      return imgUrl
    }
  },
  methods: {
    goToVideo() {
      let videoId = this.item.videoBaseId
      let videoDetailId = this.item.videoDetailId
      this.$emit('goToVideo', videoId, videoDetailId, this.idxInVideoList)
    }
  }
}
</script>

<style lang="postcss" module>
.list-item {
  color: #9A9A9A;
  font-weight: normal;
  &.active {
    color: #F9BE00;
    .img-border {
      border: 2px solid #FABE00;
    }
  }
  &:hover {
    color: #E9E9E9;
  }
  & > a {
    height: 56px;
    margin-bottom: 15px;
    display: block;
    width: 252px;
    position: relative;
    & > .img-wrapper {
      float: left;
      width: 100px;
      height: 100%;
      box-sizing: border-box;
      position: relative;
      & > img {
        width: 100px;
        height: 56px;
        vertical-align: bottom;
      }
      & > .img-border {
        z-index: 2;
        box-sizing: border-box;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
      }
      & > .date {
        background-color: black;
        width: 73px;
        height: 17px;
        line-height: 17px;
        text-align: center;
        position: absolute;
        right: 0;
        bottom: 0;
        opacity: 0.7;
        font-family: PingFangSC-Medium;
        font-size: 12px;
        color: #FFFFFF;
      }
    }
    & > .intro {
      float: left;
      width: 144px;
      height: 80%;
      padding-left: 8px;
      opacity: 0.9;
      font-family: PingFangSC-Medium;
      font-size: 12px;
      line-height: 1.4;
      overflow: hidden;
  ??????text-overflow: ellipsis;
  ??????white-space: nowrap;
    }
  }
}
</style>



