<template>
  <section :class="[$style['right-grid-panel'], collapsed ? $style.collapsed : '', isFullPanel ? $style.fullpanel : '']">
    <h1 :class="$style['panel-name']">
      <span>????????????</span>
    </h1>
    <h2 :class="$style['series-name']">
      <a href="javascript:void(0);">{{ videoInfos.name }}</a>
    </h2>
    <tab-row 
      v-if="numsOfTabs > 1"
      @click="handleTabSwitch"
      :tabs="tabs"
    ></tab-row>
    <grid-container
      :gridList="renderList"
      :startIndex="renderStartIndex"
      :selectedIndex="videoInfos.currentIndex"
      v-on:goToVideo="goToVideo"
    ></grid-container>
    <a :class="collapsed ? $style['left-icon-wrapper'] : $style['right-icon-wrapper']" href="javascript:void(0);" @click="toggleDrawer">
      <span class="iconfont" :class="[collapsed ? 'icon-left' : 'icon-right', $style['left-right-icon']]"></span>
    </a>
  </section>
</template>

<script>
import tabRow from './tab-row.vue'
import gridContainer from './grid-container.vue'

export default {
  name: 'right-grid-panel',
  components: {
    'tab-row': tabRow,
    'grid-container': gridContainer,
  },
  props: {
    videoInfos: {
      type: Object,
      required: true
    },
    isFullPanel: {
      type: Boolean,
      required: false
    },
    isPanelDisplay: {
      type: Boolean,
      required: false
    }
  },
  data() {
    return {
      collapsed: false,
      epsPerTab: 30, // numbers of episodes per tab
      numsOfTabs: 0, // numbers of tabs
      lenOfList: 0, // the total length of the list
      tabs: [], // tab list/array
      videoList: [], // the video list rendered from parent ele
      renderList: [], // the list to be rendered for one tab
      renderStartIndex: 0, // the render start index
      currentIndex: 0, // video current index in the video list
    }
  },
  computed: {
  },
  watch: {
    videoInfos: {
      handler(newInfos, oldInfos) {
        this.setActiveTabByVideoIndex(newInfos.currentIndex)
      },
      deep: true
    },
    isPanelDisplay(newValue, oldValue) {
      this.collapsed = !newValue
    }
  },
  created() {
    // ?????????????????????(?????????create) ???????????????video.vue??????videoInfos??????????????????
    // this.repeatifyArr() // mock data.
    this.init()
  },
  methods: {
    goToVideo(videoId, videoDetailId, idxInVideoList) {
      this.$emit('goToVideo', videoId, videoDetailId, idxInVideoList)
    },
    setActiveTabByVideoIndex(videoIndex) {
      for (let i = 0; i < this.tabs.length; i++) {
        if (this.tabs[i].start <= videoIndex + 1 && videoIndex + 1 <= this.tabs[i].end) {
          this.handleTabSwitch(i)
        }
      }
    },
    init() {
      // set lenOfList
      let vList = this.videoList = this.videoInfos.vVideoDetailRspVos
      this.lenOfList = vList.length

      // set currentIndex
      this.currentIndex = this.videoInfos.currentIndex

      // set numsOfTabs
      this.numsOfTabs = Math.ceil(this.lenOfList / this.epsPerTab)

      // set tabs array
      this.setTabs()
      // initialize renderList
      this.initRenderList()
    },
    initRenderList() {
      if (this.lenOfList <= this.epsPerTab) {
        this.renderList = this.videoList
        return
      }
      let len = this.tabs.length
      for (let i = 0; i < len; i++) {
        if (this.tabs[i].active) {
          this.handleTabSwitch(i)
        }
      }
    },
    setTabs() {
      let len = this.lenOfList
      let epsPerTab = this.epsPerTab
      let numsOfTabs = this.numsOfTabs

      for (let i = 0; i < numsOfTabs; i++) {
        let start = i * epsPerTab + 1
        let end = (i + 1) * epsPerTab
        end = Math.min(end, len)
        let tab = this.createTabObj(start, end)
        this.tabs.push(tab)
      }
    },
    createTabObj(start, end) {
      let active = this.isTabActive(start, end)
      let tab = {}
      tab.start = start // start???1??????
      tab.end = end
      let s = start < 10 ? '0' + start : start
      let e = end < 10 ? '0' + end : end
      let label = s + '-' + e
      tab.label = label
      tab.active = active
      return tab
    },
    isTabActive(start, end) {
      return (this.currentIndex + 1) >= start && (this.currentIndex + 1) <= end
    },
    repeatifyArr(arr) {
      let vList = this.videoInfos.vVideoDetailRspVos
      let rptTimes = 50
      for (let i = 0; i < rptTimes; i++) {
        vList.push(vList[0])
      }
    },
    handleTabSwitch(index) {
      let len = this.lenOfList
      let epsPerTab = this.epsPerTab
      let vList = this.videoList

      let start = index * epsPerTab
      let end = (index + 1) * epsPerTab
      end = Math.min(end, len)
      this.renderList = vList.slice(start, end)
      this.renderStartIndex = start

      // change tabs class
      let tabs = this.tabs
      let length = tabs.length
      for (let i = 0; i < length; i++) {
        tabs[i].active = false
      }

      tabs[index].active = true
    },
    toggleDrawer() {
      this.collapsed = !this.collapsed
      let width = this.collapsed ? 1140 : 870
      this.$emit('changeVideoWidth', width)
    },
  }
}
</script>

<style lang="postcss" module>
.right-grid-panel {
  /* transition: width .2s ease; */
  width: 280px;
  height: 530px;
  background-color: #161616;
  position: absolute;
  top: 0;
  right: 0;
  overflow-x: hidden;
  &.fullpanel {
    opacity: 0.95;
    height: 100%;
    position: fixed;
    & > .right-icon-wrapper, & > .left-icon-wrapper {
      display: none;
    }
    &.collapsed {
      width: 0;
      background-color: black;
    }
  }
  & > .panel-name {
    width: 100%;
    font-family: PingFangSC-Medium;
    font-size: 16px;
    color: #FABE00;
    letter-spacing: -0.24px;
    height: 47px;
    line-height: 47px;
    border-bottom: 1px solid #272727;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-weight: normal;
    & > span {
      padding-left: 18px;
    }
  }
  & > .series-name {
    font-family: PingFangSC-Medium;
    font-size: 16px;
    color: #9A9A9A;
    letter-spacing: -0.24px;
    margin-top: 15px;
    margin-bottom: 10px;
    margin-left: 18px;
    font-weight: normal;
    & > a {
      overflow: hidden;
      display: inline-block;
      width: 251px;
      height: 18px;
    }
  }
  & > .right-icon-wrapper, & > .left-icon-wrapper {
    display: block;
    position: absolute;
    background-color: #2D2D2D;
    width: 13px;
    height: 74px;
    line-height: 74px;
    left: 0;
    top: 228px;
    & > .left-right-icon {
      color: #4a4a4a;
    }
    &:hover {
      background-color: #666666;
    }
  }
  & > .right-icon-wrapper {
    border-radius: 0 3px 3px 0;
  }
  & > .left-icon-wrapper {
    border-radius: 3px 0 0 3px;
  }
  &.collapsed {
    width: 13px;
    background-color: black;
  }
}
</style>



