<script>
import ua from '@/utils/ua'

export default {
  methods: {
    prefixImgUrl(url, size, prefix = 'http://dx-image-test.itangchao.me/') {
      if (DEBUG || BETA) {
        prefix = 'https://image.youxiang0210.com/'
      }
      if (size) {
        return prefix + url + '!' + size
      }
      return prefix + url
    },
    adaptImgUrl(url, width, height = 410, q = 100) {
      if (!width) {
        width = document.body.offsetWidth
      }
      if (ua.mac) {
        width = width * 1
        height = height * 1
      }
      let suffix = `?imageView2/2/w/${width}/h/${height}/q/${q}|imageslim`
      return url + suffix
    },
    getSchemeUrl(url) {
      let ret = '#'
      let reg = /.*action=(\w+)&(\w+)=(.*)/
      let matches = url.match(reg)
      if (!matches) return ret
      let action = matches[1]
      let scheme = matches[3]
      switch (action) {
        case 'videoPlay':
          ret = `/play.html?videoId=${scheme}`
          break
        case 'webViewExternal':
          ret = scheme
          break
        default:
          break
      }
      return ret
    },
    getUrlParam(name) {
      const reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)')
      const r = window.location.search.substr(1).match(reg)
      return r !== null ? decodeURIComponent(r[2].replace(/(\+)+/g, ' ')) : null
    },
  }
}
</script>



