<template>
<div
  :class="$style.eSlide"
  ref="slide"
  @mouseenter.stop="stopTimer"
  @mouseleave.stop="startTimer"
>
  <ul
    v-show="mounted"
    :class="$style.eSlideWrap"
    :style="{
      width: items.length * 100 + '%',
      msTransform: `translateX(${ translate }px)`,
      webkitTransform: `translateX(${ translate }px)`,
      transform: `translateX(${ translate }px)`
    }"
  >
    <slide-item
      v-for="(item, index) in items"
      :key="index"
      :item="item"
      :width="slideWidth"
      @click="handleItemClick(item.schemeUrl)"
    >
    </slide-item>
  </ul>
  <ul
    v-show="indicator && !hiddenIndicator"
    :class="$style.eSlideIndicators"
    ref="indicator"
  >
    <li
      v-for="(item, index) in items"
      :key="index"
      :class="[
        $style.eSlideIndicator,
        activeIndex === index ? $style.isActive : ''
      ]"
      @mouseenter="handleIndicatorHover(index)"
      @click="handleItemClick(item.schemeUrl)"
    >
    {{ item.title }}
    </li>
  </ul>
</div>
</template>

<script>
import { throttle } from '../../utils'
import commonMixins from '@/mixins/common'
import slideItem from './slide-item.vue'

export default {
  name: 'e-slide',

  mixins: [commonMixins],

  components: {
    'slide-item': slideItem
  },

  data() {
    return {
      activeIndex: 0,
      translate: 0, // ?????????
      slideWidth: 0, // ????????????
      indicatorHeight: 0, // ???????????????
      hiddenIndicator: false, // ?????????????????????
      mounted: false, // dom???????????????
      timer: null,
      hover: false, // hover??????
    }
  },

  props: {
    items: {
      type: Array,
      default: () => []
    },
    indicator: {
      type: Boolean,
      default: true
    },
    autoPlay: {
      type: Boolean,
      default: true
    },
    duration: {
      type: Number,
      default: 5000
    }
  },

  watch: {
    activeIndex(newVal) {
      this.translate = -this.activeIndex * this.slideWidth
    }
  },

  methods: {
    handleItemClick(url) {
      window.open(this.getSchemeUrl(url))
    },
    setItem(index) {
      this.activeIndex = index
    },
    handlePlay() {
      if (this.activeIndex < this.items.length - 1) {
        this.activeIndex++
      } else {
        this.activeIndex = 0
      }
    },
    startTimer() {
      if (!this.autoPlay) return
      this.timer = setInterval(this.handlePlay, this.duration)
    },
    stopTimer() {
      if (!this.autoPlay) return
      clearInterval(this.timer)
    },
    resetSlide() {
      this.slideWidth = this.$refs.slide.offsetWidth
      let slideHeight = this.$refs.slide.offsetHeight
      if (slideHeight <= this.indicatorHeight) {
        this.hiddenIndicator = true
      } else {
        this.hiddenIndicator = false
      }
    },
  },

  created() {
    this.handleIndicatorHover = index => {
      this.setItem(index)
    }
    this.handleResize = throttle(this.resetSlide, 200)
  },

  mounted() {
    this.slideWidth = this.$refs.slide.offsetWidth
    this.indicatorHeight = this.$refs.indicator.offsetHeight
    this.mounted = true
    if (this.autoPlay) {
      this.startTimer()
    }
  },
}
</script>

<style module>
.e-slide {
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 410px;
  &__wrap {
    overflow: hidden;
    transition: transform 0.4s ease-in-out;
  }
  &__indicators {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(-50%, -50%);
    min-width: 200px;
    max-width: 235px;
    padding: 10px 0;
    background: rgba(255, 255, 255, 0.9);
    border-radius: 4px;
    font-size: 0px;
  }
  &__indicator {
    padding: 10px 15px;
    color: #000;
    font-size: 14px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
    &.is-active, &:hover {
      background: #F2BB3C;
      color: #fff;
    }
  }
}
</style>



