<template>
  <vue-scrollbar :class="$style['my-scrollbar']" ref="Scrollbar">
    <ul :class="['u-clearfix',$style['grid-container']]">
      <grid-item 
        v-for="(item, index) in gridList"
        :item="item"
        :label="item.episodeDisplay"
        :active="isActive(startIndex, index)"
        :key="index"
        :idxInVideoList="idxInVideoList(startIndex, index)"
        v-on:goToVideo="goToVideo"
      >
      </grid-item>
    </ul>
  </vue-scrollbar>
</template>

<script>
import gridItem from './grid-item.vue'
import VueScrollbar from '../../common/scroll-bar/vue-scrollbar'

export default {
  name: 'grid-container',
  components: {
    'grid-item': gridItem,
    'vue-scrollbar': VueScrollbar,
  },
  props: {
    gridList: {
      type: Array,
      default: () => []
    },
    startIndex: {
      type: Number,
      default: 0,
    },
    selectedIndex: {
      type: Number,
      default: 0,
    }
  },
  methods: {
    idxInVideoList(startIndex, index) {
      return startIndex + index
    },
    getLabel(startIndex, index) {
      let num = startIndex + index
      let label = num < 9 ? '0' + (num + 1) : num + 1 + ''
      return label
    },
    isActive(startIndex, index) {
      return this.startIndex + index === this.selectedIndex
    },
    goToVideo(videoId, videoDetailId, idxInVideoList) {
      this.$emit('goToVideo', videoId, videoDetailId, idxInVideoList)
    }
  },
  data() {
    return {
    }
  },
  created() {
  },
  mounted() {
  },
  computed: {
  },
}
</script>

<style lang="postcss" module>
.grid-container {
  width: 251px;
  margin-left: 17px !important;
  margin-top: 3px !important;
}
.my-scrollbar {
  height: 398px;
}
</style>



