<template>
<div
  :class="$style.backTop"
  v-if="topBackShow"
  @click="backTop"
>
  <div :class="$style.backTopIcon">
    <i class="iconfont icon-back"></i>
  </div>
  <p>????????????</p>
</div>
</template>

<script>
export default {
  name: 'e-back-top',

  data() {
    return {
      topBackShow: false,
      scrollThreshold: 570, // ????????????
      scrollTopDistance: 0,
    }
  },

  mounted() {
    document.onscroll = () => {
      this.scrollTopDistance = document.documentElement.scrollTop || document.body.scrollTop
      if (this.scrollTopDistance >= this.scrollThreshold) {
        this.topBackShow = true
      } else {
        this.topBackShow = false
      }
    }
  },

  methods: {
    backTop() {
      const currScroll = this.scrollTopDistance
      clearInterval(this.timer)
      this.timer = setInterval(() => {
        let speed = (0 - currScroll) / 5
        speed = speed > 0 ? Math.ceil(speed) : Math.floor(speed)
        if (this.scrollTopDistance <= 0) {
          clearInterval(this.timer)
        }
        if (document.body.scrollTop) {
          document.body.scrollTop = this.scrollTopDistance + speed
        } else {
          document.documentElement.scrollTop = this.scrollTopDistance + speed
        }
        document.documentElement.scrollTop = this.scrollTopDistance + speed
      }, 15)
    },
  },
}
</script>

<style lang="postcss" module>
.backTop {
  overflow: hidden;
  position: fixed;
  bottom: 100px;
  right: 30px;
  width: 56px;
  cursor: pointer;
  z-index: 100;
  .backTopIcon {
    background: #e7e7e7;
    opacity: 0.53;
    width: 56px;
    height: 56px;
    line-height: 56px;
    border-radius: 100%;
    text-align: center;
    i {
      color: #606060;
      font-size: 30px;
      font-weight: bold;
    }
    &:hover {
      opacity: 1;
      background: #FABE00;
      i {
        color: #fff;
      }
    }
  }
  p {
    color: #9D9D9D;
    text-align: center;
    margin-top: 6px;
    font-size: 12px;
  }
}
</style>



