<template>
  <vue-scrollbar :class="$style['my-scrollbar']" ref="Scrollbar">
    <ul :class="['u-clearfix',$style['list-container']]">
      <list-item 
        v-for="(item, index) in renderList"
        :item="item"
        :key="index"
        :active="isActive(index)"
        :idxInVideoList="index"
        v-on:goToVideo="goToVideo"
      >
      </list-item>
    </ul>
  </vue-scrollbar>
</template>

<script>
import listItem from './list-item.vue'
import VueScrollbar from '../../common/scroll-bar/vue-scrollbar'

export default {
  name: 'list-container',
  components: {
    'list-item': listItem,
    'vue-scrollbar': VueScrollbar,
  },
  props: {
    renderList: {
      type: Array,
      default: () => []
    },
    selectedIndex: {
      type: Number,
      default: 0,
    },
    scrollbarHeight: {
      type: Number,
    }
  },
  data () {
    return {
      itemH: 74, // list item height
      baseTop: 16 // equals the padding-top value between `scrollbar` and `ul`
    }
  },
  created() {
  },
  mounted() {
  },
  computed: {
  },
  watch: {
    scrollbarHeight(newHeight, oldHeight) {
      if (newHeight) {
        this.$el.style.height = newHeight + 'px'
      }
    },
    selectedIndex(newIndex, oldIndex) {
      this.scrollByVideoIndex(newIndex)
    }
  },
  methods: {
    goToVideo(videoId, videoDetailId, idxInVideoList) {
      this.scrollByVideoIndex(idxInVideoList)
      this.$emit('goToVideo', videoId, videoDetailId, idxInVideoList)
    },
    isActive(index) {
      return index === this.selectedIndex
    },
    scrollByVideoIndex(vIndex) {
      let itemTop = vIndex * this.itemH + this.baseTop
      let scrollTop = this.$refs.Scrollbar.top
      let deltaY = itemTop - scrollTop
      if (deltaY < 0) { // ?????????item?????????????????????
        this.$refs.Scrollbar.scrollToY(itemTop - 10)
      } else if (deltaY > 340) { // 340 = scrollWrapperHeight - 58  ?????????item?????????????????????
        this.$refs.Scrollbar.scrollToY(scrollTop + 50)
      }
    }
  }
}
</script>

<style lang="postcss" module>
.list-container {
  width: 251px;
  margin-left: 19px !important;
  margin-top: 16px !important;
}
.my-scrollbar {
  height: 398px;
}
</style>



