function getQuerys(params) {
  let ret = ''
  Object.keys(params)
    .forEach(key => {
      ret += `&${encodeURIComponent(key)}=${encodeURIComponent(params[key])}`
    })
  if (ret) {
    ret = ret.replace(/&/, '?')
  }
  return ret
}

export default async function fetcher(url, params = {}, type = 'POST') {
  let prefix, mode, credentials
  if (DEBUG && !BETA) { // prefix for api calls
    prefix = 'http://10.0.31.116:8081'
  } else if (BETA) {
    // ???????????????nginx????????????
    prefix = ''
  } else {
    // ???????????????nginx????????????
    prefix = ''
  }
  if (params.customPrefix) { // custom prefix
    prefix = params.customPrefix
    delete params.customPrefix
  }
  type = type.toUpperCase()
  url = prefix + url
  mode = params.mode || 'cors'
  credentials = params.credentials || 'include'

  let options = Object.assign({}, {
    method: type,
    mode,
    credentials,
    headers: {
      'Content-Type': 'application/json',
    },
  })

  switch (type) {
    case 'GET':
      url += getQuerys(params)
      break
    case 'POST':
      options.body = JSON.stringify(params)
      break
    case 'PUT':
      options.body = JSON.stringify(params)
      break
    case 'DELETE':
      options.body = JSON.stringify(params)
      break
    default:
      break
  }

  try {
    let response = await fetch(url, options)
    let resStatus = response.status
    let resJson = response.json()
    if (resStatus >= 400) {
      // TODO handle error
    } else {
      return resJson
    }
  } catch (error) {
    console.log(error)
    return error
  }
}



