<template>
  <div>
    <div :class="$style['share-dialog']" class="dialog">
      <p :class="$style['dialog-name']">
        ????????????
        <i :class="$style['icon']" class="iconfont icon-close" @click="closeDialog"></i>
      </p>
      <p :class="$style['title']">
        <span :class="$style['name']">{{ videoInfos.name }}</span><span v-if="videoInfos.categories !== 1" :class="$style['episode']">{{ episodeDisplay }}</span>
      </p>
      <div :class="$style['intro-wrapper']">
        <p :class="$style['intro']">{{ videoInfos.intro }}</p>
      </div>
      <p :class="$style['desc']">???????????????????????????????????????????????????</p>
      <div :class="$style['qrcode']" class="qrcode"></div>
      <p :class="$style['qr-txt']">?????????????????????</p>
    </div>
    <div class="mask"></div>
  </div>
</template>

<script>
import QRCode from '../../utils/external/qrcode.js'
import { debounce } from '../../utils'
export default {
  name: 'share-dialog',
  props: {
    videoInfos: {
      type: Object,
    }
  },
  data() {
    return {
      // dialog style data
      dialogWidth: 390
    }
  },
  components: {
  },
  created() {
  },
  mounted() {
    this.locateDialog()
    this.onWindowResize()
    this.initQR()
  },
  computed: {
    episodeDisplay() {
      let currentIndex = this.videoInfos.currentIndex
      let episodeDisplay = this.videoInfos.vVideoDetailRspVos[currentIndex].episodeDisplay
      if (this.videoInfos.categories === 2 && this.videoInfos.bizType === 1) { // ??????????????????
        episodeDisplay = '???' + episodeDisplay + '???'
      }
      return episodeDisplay
    },
    qrText() {
      return window.location.href
    }
  },
  methods: {
    initQR() {
      var qrcode = new QRCode(this.$el.querySelector('.qrcode'), {
        // width: 100,
        // height: 100
      })
      qrcode.makeCode(this.qrText)
    },
    closeDialog() {
      this.$emit('closeDialog')
    },
    locateDialog() {
      let w = Math.max(document.documentElement.clientWidth, window.innerWidth || 0)
      let h = Math.max(document.documentElement.clientHeight, window.innerHeight || 0)
      let dialog = this.$el.querySelector('.dialog')
      dialog.style.left = (w - this.dialogWidth) / 2 + 'px'
      dialog.style.zIndex = 3

      let mask = this.$el.querySelector('.mask')
      mask.style.position = 'fixed'
      mask.style.left = 0
      mask.style.top = 0
      mask.style.height = h + 'px'
      mask.style.background = 'rgb(0, 0, 0)'
      mask.style.opacity = 0.5
      mask.style.zIndex = 2
      mask.style.width = w + 'px'
    },
    relocateDialog: debounce(function() {
      this.locateDialog()
    }, 200),
    onWindowResize() {
      let self = this
      window.onresize = function() {
        self.relocateDialog()
      }
    }
  }
}
</script>

<style lang="postcss" module>
  .is-hidden { 
    display: none;
  }
  .share-dialog {
    position: fixed;
    top:247px;
    width: 390px;
    height: 300px;
    background: #FFFFFF;
    border: 1px solid #DBDBDB;
    border-radius: 3px;
    & > .dialog-name {
      background: #F8F8F8;
      line-height: 30px;
      width: 100%;
      position: absolute;
      top: 0;
      right: 0;
      opacity: 0.9;
      font-family: PingFangSC-Semibold;
      font-size: 12px;
      color: #9D9D9D;
      text-align: center;
      & > .icon {
        right: 10px;
        top: -2px;
        font-size: 17px;
        position: absolute;
        cursor: pointer;
      }
    }
    & > .title {
      opacity: 0.9;
      font-family: PingFangSC-Medium;
      font-size: 16px;
      color: #FF9F00;
      position: absolute;
      margin-top: 53px;
      width: 100%;
      text-align: center;
      & > .name {
        display: inline-block;
        max-width: 200px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        padding-right: 5px;
      }
      & > .episode {
        display: inline-block;
        max-width: 180px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    & > .intro-wrapper {
      width: 100%;
      position: absolute;
      margin-top: 76px;
      & > .intro {
        opacity: 0.9;
        font-family: PingFangSC-Regular;
        font-size: 12px;
        color: #9D9D9D;
        width: 310px;
        margin: 0 auto;
        text-align: center;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    } 
    & > .desc {
      font-family: PingFangSC-Regular;
      font-size: 11px;
      color: #272727;
      letter-spacing: -0.07px;
      position: absolute;
      margin-top: 107px;
      width: 100%;
      text-align: center;
    }
    & > .qrcode {
      position: absolute;
      display: block;
      border: 2px solid #FABE00;
      width: 120px;
      height: 120px;
      box-sizing: border-box;
      top: 135px;
      left: 135px;
      & > img {
        position: relative;
        width: 112px;
        height: 112px;
        top: 2px;
        left: 2px;
      }
    }
    & > .qr-txt {
      font-family: PingFangSC-Regular;
      font-size: 10px;
      color: #969696;
      letter-spacing: -0.06px;
      position: absolute;
      margin-top: 260px;
      width: 100%;
      text-align: center;
    }
  }
</style>



