<template>
  <div :class="$style['video-wrap']" :style="{visibility: rendered}">
    <right-grid-panel
      class="right-panel"
      v-if="videoInfos.categories == 2 && videoInfos.bizType == 1"
      v-on:changeVideoWidth="changeVideoWidth"
      :videoInfos="videoInfos"
      v-on:goToVideo="goToVideo"
      :isFullPanel="isFullPanel"
      :isPanelDisplay="isPanelDisplay"
      >
    </right-grid-panel>
    <right-list-panel
      class="right-panel"
      v-else-if="videoInfos.categories == 2 && videoInfos.bizType == 2"
      v-on:changeVideoWidth="changeVideoWidth"
      :videoInfos="videoInfos"
      v-on:goToVideo="goToVideo"
      :isFullPanel="isFullPanel"
      :isPanelDisplay="isPanelDisplay"
      >
    </right-list-panel>
    <video :class="['vjs-elephant', 'vjs-no-flex', 'video-js', $style['video-content']]"></video>
    <share-dialog :videoInfos="videoInfos" v-if="isShareActive" v-on:closeDialog="closeDialog"></share-dialog>
  </div>
</template>

<script>
const videojs = window.videojs
import { throttle, storage, draggable, debounce } from '../../utils'
import './plugins/videojs-resolution-switcher'
import './plugins/videojs-resolution-switcher/hls-switcher'
import './plugins/videojs-hotkeys'
import commonMixins from '@/mixins/common'
import ua from '../../utils/ua'

export default {
  name: 'video-player',

  mixins: [commonMixins],

  props: {
    options: {
      type: Object,
      required: true
    },
    videoInfos: {
      type: Object,
      required: true
    },
    hlsList: {
      type: Array,
      required: true
    },
    videoExists: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      rendered: 'hidden',
      isShareActive: false, // ?????????????????? ????????????
      manualMiniClose: false, // ????????????mini?????????
      duration: 0,
      dragBinded: false,
      timer: false, // ??????miniPlayer???mousemove??????
      isPlaying: false, // ??????????????????????????????????????????
      isFullPanel: false, // ????????????panel?????????
      isPanelDisplay_saved: true,
      isPanelDisplay: true, // ????????????panel??????
      initialVolumeChange: true,
    }
  },
  mounted () {
    if (document.addEventListener) {
      document.addEventListener('scroll', this.showMiniPlayer, false)
    } else {
      document.attachEvent('scroll', this.showMiniPlayer)
    }
  },
  beforeDestory () {
    this.dispose()
  },
  computed: {
    // ?????????????????????????????????
    isNextBtnDisabled() {
      let vInfo = this.videoInfos
      if (!vInfo.videoBaseId) return
      let currentIndex = vInfo.currentIndex
      let videoList = vInfo.vVideoDetailRspVos
      let isZongyi = vInfo.categories === 2 && vInfo.bizType === 2

      let disabled = false
      if (!isZongyi) { // ????????????
        disabled = currentIndex + 1 >= videoList.length
      } else { // ?????????
        disabled = currentIndex === 0
      }
      return disabled
    },
    // ????????????index(????????????????????????nextIndex???0)
    nextVideoIndex() {
      let vInfo = this.videoInfos
      let currentIndex = vInfo.currentIndex
      let videoList = vInfo.vVideoDetailRspVos
      let isZongyi = vInfo.categories === 2 && vInfo.bizType === 2
      let nextIndex = 0
      if (!isZongyi) { // ????????????
        nextIndex = currentIndex + 1 >= videoList.length ? 0 : currentIndex + 1
      } else { // ?????????
        nextIndex = currentIndex - 1 < 0 ? videoList.length - 1 : currentIndex - 1
      }
      return nextIndex
    },
    currentDetail() {
      if (!this.videoInfos.vVideoDetailRspVos) return
      return this.videoInfos.vVideoDetailRspVos[this.videoInfos.currentIndex]
    },
    savedDuration() {
      return this.currentDetail.duration
    }
  },
  created() {
  },
  watch: {
    'options.width': function(newWidth, oldWidth) {
      if (this.player) {
        // ??????????????????
        this.player.width(newWidth)
      }
      if (!this.player) this.init()
    },
    'options.sources': {
      handler: function(newSources, oldSources) {
        if (this.player) {
          if (ua.ie10) {
            this.player.reset()
            this.player.updateSrc(newSources)
            // FIXME ????????????
            let storeHlsMap = this.getMP4Index(newSources)
            setTimeout(() => {
              this.player.currentResolution(storeHlsMap)
            }, 0)
          } else {
            this.player.src(newSources)
          }
          this.changeNextBtnStatus()
        }
        if (!this.player) this.init()
      },
      deep: true
    },
    videoExists(newValue, oldValue) { // ????????????????????????????????????
      if (!newValue) {
        this.init()
      }
    }
  },
  methods: {
    // ??????isNextBtnDisabled ??????NextButton???????????????
    changeNextBtnStatus() {
      if (this.isNextBtnDisabled) {
        let nextBtn = this.player.controlBar.NextButton.el_
        nextBtn.setAttribute('disabled', true)
        nextBtn.setAttribute('style', 'cursor:not-allowed;')
      } else {
        let nextBtn = this.player.controlBar.NextButton.el_
        nextBtn.removeAttribute('disabled')
        nextBtn.setAttribute('style', 'cursor:pointer;')
      }
    },
    closeDialog() {
      this.isShareActive = false
      if (this.isPlaying) {
        // ????????????
        this.player.play()
      }
    },
    goToVideo(videoId, videoDetailId, idxInVideoList) {
      this.$emit('goToVideo', videoId, videoDetailId, idxInVideoList)
      this.manualMiniClose = false
    },
    changeVideoWidth(width) {
      this.isPanelDisplay = this.isPanelDisplay_saved = (width === 870)
      this.$emit('changeVideoWidth', width)
    },
    addControlbarItems(player) {
      // get controlBar element
      let controlBar = player.controlBar.el_

      // insert qq & weibo sharing icons
      let shareWrapper = document.createElement('div')
      let aWeibo = document.createElement('a')
      let aQq = document.createElement('a')
      let aWeixin = document.createElement('a')
      let label = document.createElement('span')

      // let title = this.videoInfos.name
      // let url = window.location.href
      // let url = 'http://www.iqiyi.com/v_19rragsjw8.html?list=19rrkd45sy'
      // let url = 'https://www.youtube.com/watch?v=8WEqxr9UpFQ'
      // let url = 'http://www.iqiyi.com/v_19rr9x93h8.html'
      // let url = 'http://v.youku.com/v_show/id_XMjY4OTY3MDAwNA==.html?f=26486104&spm=a2hww.20023042.m_223465.5~5~5~5!2~5!2~5~A&from=y1.3-idx-beta-1519-23042.223465.5-1'
      // let url = 'http%3A%2F%2Fv-wb.youku.com%2Fv_show%2Fid_XMjY4OTY3MDAwNA%3D%3D.html'
      // let url = 'http://v-wb.youku.com/v_show/id_XMjY3NjQ2OTAzNg==.html?spm=a2hww.20023042.ykRecommend.5~5!3~5~5~A'
      // let url = 'http%3A%2F%2Fv-wb.youku.com%2Fv_show%2Fid_XMjY3NjQ2OTAzNg%3D%3D.html'
      // let appkey = 3851233615
      // let sinaShareLink = `http://v.t.sina.com.cn/share/share.php?appkey=${appkey}&url=${url}&title=${title}`
      // console.log(sinaShareLink)

      aWeibo.setAttribute('href', 'javascript:void(0);')
      aQq.setAttribute('href', 'javascript:void(0);')
      aWeixin.setAttribute('href', 'javascript:void(0);')

      aWeibo.className = 'iconfont icon-weibo'
      aQq.className = 'iconfont icon-qq'
      aWeixin.className = 'iconfont icon-wechat'
      label.className = 'label'
      label.innerHTML = '??????'

      shareWrapper.className = 'vjs-control vjs-share-control'
      shareWrapper.appendChild(label)
      // shareWrapper.appendChild(aWeibo)
      // shareWrapper.appendChild(aQq)
      shareWrapper.appendChild(aWeixin)

      let insertBeforeNode = player.controlBar.volumeControl.el_

      controlBar.insertBefore(shareWrapper, insertBeforeNode)

      let self = this
      aWeixin.addEventListener('click', function() {
        self.isPlaying = self.player.controlBar.playToggle.hasClass('vjs-playing')
        self.isShareActive = true
        // ????????????
        self.player.pause()
      })
    },
    setWatchHistory(time) {
      // ????????????????????? ??????id??????
      const watchHistory = storage.get('watchHistory') || []
      const videoData = this.videoInfos.vVideoDetailRspVos[this.videoInfos.currentIndex]
      const duration = this.duration
      const progress = Math.floor(time / duration * 100)

      const watching = { name: videoData.title, categories: this.videoInfos.categories, bizType: this.videoInfos.bizType, episodes: videoData.episodeDisplay, progress: progress, videoId: videoData.videoBaseId, videoDetailId: videoData.videoDetailId }
      let markedIndex
      for (let i = 0, j = watchHistory.length; i < j; i++) {
        if (videoData.videoBaseId === watchHistory[i].videoId) {
          markedIndex = i
          break
        }
      }

      if (markedIndex >= 0) {
        watchHistory.splice(markedIndex, 1)
        watchHistory.unshift(watching)
      } else {
        if (watchHistory.length < 10) {
          watchHistory.push(watching)
        } else {
          watchHistory.pop()
          watchHistory.unshift(watching)
        }
      }

      storage.set('watchHistory', watchHistory)
    },
    markWatching(time) {
      const watchedList = storage.get('watchedList') || {}
      watchedList[this.currentDetail.videoDetailId] = time
      storage.set('watchedList', watchedList)
    },
    buildTooltip() {
      let component = videojs.getComponent('Component')
      let tooltip = videojs.extend(component, {
        createEl() {
          return videojs.createEl('span', {
            className: 'elephant-tooltip'
          })
        },
      })
      videojs.registerComponent('Tooltip', tooltip)
    },
    buildMiniPlayerHeader() {
      let modal = videojs.getComponent('Component')
      let theComponent = videojs.extend(modal, {
        name() {
          return 'MiniplayerHeader'
        },
        constructor: function(player, options) {
          modal.apply(this, arguments)
        },
        createEl() {
          return videojs.createEl('div', {
            innerHTML: `
              <span class="text">????????????????????????</span>
              <i class="iconfont icon-close close"></i>
            `,
            className: 'mini-player-header vjs-hidden'
          })
        },
      })
      videojs.registerComponent('MiniplayerHeader', theComponent)
    },
    buildMiniPlayerPlayToggle() {
      let that = this
      let modal = videojs.getComponent('ClickableComponent')
      let theComponent = videojs.extend(modal, {
        name() {
          return 'MiniplayerPlayToggle'
        },
        constructor: function(player, options) {
          modal.apply(this, arguments)
        },
        createEl() {
          return videojs.createEl('div', {
            innerHTML: `
              <i class="iconfont icon-stop play-toggle"></i>
            `,
            className: 'mini-player-play-toggle vjs-hidden'
          })
        },
        handleClick() {
          if (that.player.paused()) {
            that.player.play()
          } else {
            that.player.pause()
          }
        }
      })
      videojs.registerComponent('MiniplayerPlayToggle', theComponent)
    },
    buildModalComp(componentName, imgPath, text, txtClass) {
      let textClass = 'error-text'
      if (txtClass) {
        textClass = 'none-text'
      }
      let modal = videojs.getComponent('Component')
      let theComponent = videojs.extend(modal, {
        name() {
          return componentName
        },
        constructor: function(player, options) {
          modal.apply(this, arguments)
        },
        createEl() {
          return videojs.createEl('div', {
            innerHTML: `
              <div class="content-wrap">
                <img src="${imgPath}" alt="">
                <span class="${textClass}">${text}</span>
              </div>
            `,
            className: 'vjs-fullscreen-modal vjs-hidden'
          })
        },
      })
      videojs.registerComponent(componentName, theComponent)
    },
    buildResChange(text) {
      let modal = videojs.getComponent('Component')
      let theComponent = videojs.extend(modal, {
        constructor: function(player, options) {
          modal.apply(this, arguments)
        },
        createEl() {
          return videojs.createEl('div', {
            innerHTML: `
              <span>????????????????????????????????????</span>
            `,
            className: 'vjs-res-change vjs-hidden'
          })
        },
      })
      videojs.registerComponent('ResChangeTip', theComponent)
    },
    buildErrorModal(componentName, imgPath, text) {
      let modal = videojs.getComponent('ClickableComponent')
      let theComponent = videojs.extend(modal, {
        name() {
          return componentName
        },
        constructor: function(player, options) {
          modal.apply(this, arguments)
        },
        createEl() {
          return videojs.createEl('div', {
            innerHTML: `
              <div class="content-wrap" style="cursor: pointer;">
                <img src="${imgPath}" alt="">
                <span class="error-text">${text}</span>
              </div>
            `,
            className: 'vjs-fullscreen-modal vjs-hidden'
          })
        },
        handleClick() {
          location.reload()
        }
      })
      videojs.registerComponent(componentName, theComponent)
    },
    buildLoadingSpinner() {
      let loadingComponent = videojs.getComponent('LoadingSpinner')
      let Loading = videojs.extend(loadingComponent, {
        constructor: function(player, options) {
          loadingComponent.apply(this, arguments)
        },
        createEl() {
          return videojs.createEl('div', {
            innerHTML: `
              <div class="content-wrap">
                <img class="loading-gif" src="${require('../../assets/common/player-loading.gif')}" alt="">
                <span class="loading-text">???????????????.....</span>
              </div>
            `,
            className: 'vjs-fullscreen-modal vjs-elephant-loading vjs-hidden'
          })
        },
      })
      videojs.registerComponent('Loading', Loading)
    },
    registerNextButton() {
      let VjsButton = videojs.getComponent('Button')
      let self = this
      let NextButton = videojs.extend(VjsButton, {
        constructor: function() {
          VjsButton.apply(this, arguments)
          if (self.isNextBtnDisabled) {
            this.el_.setAttribute('disabled', true)
            this.el_.setAttribute('style', 'cursor:not-allowed;')
          }
        },
        handleClick: function() {
          self.goToNextVideo()
        },
        buildCSSClass: function () {
          return 'vjs-control vjs-button vjs-next iconfont icon-next'
        },
        createControlTextEl: function(button) {
          button.innerHTML = ''
          return button.innerHTML
        }
      })
      videojs.registerComponent('NextButton', NextButton)
    },
    registerXuanjiButton() {
      let VjsButton = videojs.getComponent('Button')
      let self = this
      let xuanjiButton = videojs.extend(VjsButton, {
        constructor: function() {
          VjsButton.apply(this, arguments)
        },
        handleClick: function() {
          self.isPanelDisplay = !self.isPanelDisplay
          if (self.isPanelDisplay) {
            this.addClass('active')
          } else {
            this.removeClass('active')
          }
        },
        buildCSSClass: function () {
          return 'vjs-control vjs-button vjs-xuanji iconfont icon-xuanji'
        },
      })
      videojs.registerComponent('xuanjiButton', xuanjiButton)
    },
    goToNextVideo() {
      // if (!this.isNextBtnDisabled) {
      let vInfo = this.videoInfos
      let videoList = vInfo.vVideoDetailRspVos
      let videoId = vInfo.videoBaseId
      let videoDetailId = videoList[this.nextVideoIndex].videoDetailId

      this.goToVideo(videoId, videoDetailId, this.nextVideoIndex)
      // }
    },
    init() {
      const options = this.options
      const theComponent = this
      this.registerNextButton()
      this.registerXuanjiButton()
      let controlBar = {
        children: [
          'playToggle',
          'NextButton',
          'currentTimeDisplay',
          'timeDivider',
          'durationDisplay',
          'progressControl',
          'MouseTimeDisplay',
          'keepTooltipsInside',
          'fullscreenToggle',
          'volumeControl',
          'muteToggle',
          'xuanjiButton'
        ],
        progressControl: {
          keepTooltipsInside: true
        }
      }

      // ???????????????/?????????  ?????????NextButton, xuanjiButton
      if (this.videoInfos.categories === 1) {
        let index = controlBar.children.indexOf('NextButton')
        controlBar.children.splice(index, 1)
        index = controlBar.children.indexOf('xuanjiButton')
        controlBar.children.splice(index, 1)
      }

      // video.js??????options
      let videoOpiton = {
        controls: options.controls, // ????????????
        autoplay: options.autoplay || true, // ????????????
        preload: options.preload,
        poster: options.poster || '', // ??????
        width: options.width || 1150,
        height: options.height || 490,
        sources: options.sources || [],
        techOrder: ['html5', 'flash'],
        flash: { hls: { withCredentials: false }},
        html5: {
          hls: { withCredentials: false },
          hlsjsConfig: {
            debug: false
          }
        },
        controlBar,
        errorDisplay: false, // ????????????????????????
        loadingSpinner: false, // ????????????????????????
      }
      this.rendered = 'visible'
      this.player = null
      this.player = videojs(this.$el.children[0], videoOpiton, function() {
        theComponent.duration = theComponent.currentDetail ? theComponent.currentDetail.duration : 0
        theComponent.setInitCrtTimeValue(this)
        const savedOptions = storage.get('playerOptions')
        if (savedOptions && savedOptions.volume) {
          this.volume(savedOptions.volume)
          if (savedOptions.volume === 1) { // ????????????100%
            theComponent.initialVolumeChange = false
          }
        } else {
          this.volume(0.5)
        }
        // ???????????????
        theComponent.buildLoadingSpinner()
        this.addChild('Loading')
        // ???????????????
        theComponent.buildErrorModal('JCErrorDisplay', require('../../assets/common/player-error.png'), '???????????????????????????')
        this.addChild('JCErrorDisplay')
        // ?????????????????????
        theComponent.buildModalComp('JCFinish', require('../../assets/common/player-finish.png'), '???????????????')
        this.addChild('JCFinish')

        if (!theComponent.videoExists) {
          // ???????????????????????????
          theComponent.buildModalComp('JCNone', require('../../assets/common/player-none.png'), ' ??????????????????~', 'none-text')
          this.addChild('JCNone')
          this.childNameIndex_.BigPlayButton.hide()
          this.childNameIndex_.JCNone.show()
          return
        }

        theComponent.buildResChange()
        this.addChild('ResChangeTip')

        theComponent.buildTooltip()
        this.controlBar.addChild('Tooltip')

        theComponent.buildMiniPlayerHeader()
        theComponent.buildMiniPlayerPlayToggle()
        this.addChild('MiniplayerHeader')
        this.addChild('MiniplayerPlayToggle')

        const markWatchHistory = () => {
          const currentTime = this.currentTime()
          theComponent.setWatchHistory(currentTime)
          theComponent.markWatching(currentTime)
        }

        this.on('userinactive', () => {
          if (this.isFullscreen()) {
            this.childNameIndex_.ControlBar.progressControl.hide()
            this.childNameIndex_.ControlBar.el_.style = 'visibility:hidden;'
          }
        })

        this.on('useractive', () => {
          if (this.isFullscreen()) {
            this.childNameIndex_.ControlBar.progressControl.show()
            this.childNameIndex_.ControlBar.el_.style = 'visibility:visible;'
          }
        })

        this.on('timeupdate', throttle(markWatchHistory, 3000))

        this.on('error', () => {
          // FIXME ????????????????????????????????????????
          this.childNameIndex_.JCErrorDisplay.show()
          // this.childNameIndex_.Loading.hide()
          this.childNameIndex_.Loading.el_.style = 'display:none;'
        })

        this.on('loadeddata', () => {
          this.childNameIndex_.JCErrorDisplay.hide()
          this.childNameIndex_.Loading.el_.removeAttribute('style')
        })

        function isEmpty (obj) {
          for (let prop in obj) {
            return false
          }
          return true
        }

        this.on('durationchange', () => {
          this.duration(this.savedDuration)
        })

        this.on('loadedmetadata', () => {
          this.childNameIndex_.Loading.hide()
          if (this.isFullscreen()) {
            theComponent.isPanelDisplay = false
            this.childNameIndex_.ControlBar.xuanjiButton.removeClass('active')
          }
          const savedWatch = storage.get('watchedList')
          if (!isEmpty(savedWatch) && savedWatch[theComponent.currentDetail.videoDetailId]) {
            if (savedWatch[theComponent.currentDetail.videoDetailId] >= theComponent.duration) return
            let currentTime = savedWatch[theComponent.currentDetail.videoDetailId]
            theComponent.setCurrentTimeLabel(currentTime)

            this.currentTime(savedWatch[theComponent.currentDetail.videoDetailId])
          }
          if (ua.ie10) {
          } else {
            let repLabelMap = {
              '240P': '??????',
              '480P': '??????',
              '720P': '??????',
              '1080P': '1080P',
            }
            const videoBaseUrl = 'http://dx-video-test.itangchao.me/'
            let sourcesList = theComponent.currentDetail.videoResourceStatusVo.m3u8Clarity.claritys
            let levels = theComponent.hlsList
            if (levels.length > 0) {
              let hlsSourcesList = []
              for (let i = 0, j = sourcesList.length; i < j; i++) {
                hlsSourcesList.push({ url: videoBaseUrl + levels[i].uri, label: repLabelMap[sourcesList[i]], type: 'application/x-mpegURL', res: sourcesList[i] })
              }
              // sort hlsSourcesList
              hlsSourcesList = hlsSourcesList.sort(theComponent.compareResolutions)
              let index = theComponent.getHlsIndex(hlsSourcesList)
              this.updateHlsSrc(hlsSourcesList, index)
            }
          }
        })

        // loading
        const loadingHideList = ['playing', 'canplay', 'ready']
        for (let i = 0, j = loadingHideList.length; i < j; i++) {
          this.on(loadingHideList[i], () => {
            this.childNameIndex_.Loading.hide()
          })
        }
        this.on('seeking', () => {
          // update currentTime while seeking
          this.controlBar.currentTimeDisplay.updateContent()
          // ??????????????????????????????????????????
          this.controlBar.progressControl.seekBar.update()
          // keep playing status while scrubbing
          if (this.scrubbing()) {
            this.controlBar.playToggle.handlePlay()
          }
        })
        this.on('seeked', () => {
          if (this.paused()) {
            this.controlBar.playToggle.handlePause()
          }
          this.childNameIndex_.Loading.hide()
          this.childNameIndex_.JCFinish.hide()
        })

        this.on('waiting', () => {
          this.childNameIndex_.JCFinish.hide()
          // if (this.currentTime() >= this.bufferedEnd()) { // IE??????????????????
          this.childNameIndex_.Loading.show()
          // }
        })

        this.on('ended', () => {
          const savedWatch = storage.get('watchedList')
          delete savedWatch[theComponent.currentDetail.videoDetailId]
          storage.set('watchedList', savedWatch)
          if (theComponent.isNextBtnDisabled) {
            this.childNameIndex_.JCFinish.show()
            if (theComponent.videoInfos.categories === 1 || this.isFullscreen()) { // ??????????????????????????????
              this.exitFullscreen()
              this.play() // ??????????????????????????????
            } else if (theComponent.videoInfos.categories === 2) { // ????????????????????????????????????
              theComponent.goToNextVideo()
            }
          } else {
            theComponent.goToNextVideo()
          }
          // ?????????????????????
        })

        this.on('play', () => {
          videojs.removeClass(this.childNameIndex_.MiniplayerPlayToggle.el_.firstElementChild, 'icon-play2')
          videojs.addClass(this.childNameIndex_.MiniplayerPlayToggle.el_.firstElementChild, 'icon-stop')
        })

        this.on('pause', () => {
          videojs.addClass(this.childNameIndex_.MiniplayerPlayToggle.el_.firstElementChild, 'icon-play2')
          videojs.removeClass(this.childNameIndex_.MiniplayerPlayToggle.el_.firstElementChild, 'icon-stop')
        })

        // ????????????
        this.on('fullscreenchange', (screen) => {
          if (this.isFullscreen()) {
            this.childNameIndex_.ResChangeTip.addClass('fullscreen')
            this.addClass('vjs-elephant-fullscreen')
            if (theComponent.videoInfos.categories === 2) {
              // ????????????
              theComponent.changeXuanjiPanel()
            }
          } else {
            this.childNameIndex_.ResChangeTip.removeClass('fullscreen')
            this.removeClass('vjs-elephant-fullscreen')
            if (theComponent.videoInfos.categories === 2) {
              // ????????????
              theComponent.restoreXuanjiPanel()
            }
            this.childNameIndex_.ControlBar.progressControl.show()
            this.childNameIndex_.ControlBar.el_.style = 'visibility:visible;'
          }

          // ??????button?????????title
          const hoverButtons = ['playToggle', 'muteToggle']
          for (let i = 0, j = hoverButtons; i < j; i++) {
            this.controlBar[i].el_.removeAttribute('title')
          }
        })

        this.on('resolutionchange', () => {
          if (this.isFullscreen()) {
            this.childNameIndex_.ResChangeTip.addClass('fullscreen')
          } else {
            this.childNameIndex_.ResChangeTip.removeClass('fullscreen')
          }
          theComponent.setCurrentTimeLabel(this.currentTime())
          this.childNameIndex_.ResChangeTip.el_.firstElementChild.innerHTML = `<span>????????????????????????????????????</span>`
          this.childNameIndex_.ResChangeTip.show()
        })
        this.on('reschangeFinish', (event, label) => {
          this.childNameIndex_.ResChangeTip.el_.firstElementChild.innerHTML = `<span>??????????????????<b class="res-label">${label}</b>?????????</span>`
          theComponent.hideResolutionChangeTip() // debounced function
        })

        // ??????????????????title?????????
        this.on('enterFullWindow', (screen) => {
          this.controlBar['fullscreenToggle'].el_.removeAttribute('title')
        })
        this.on('exitFullWindow', (screen) => {
          this.controlBar['fullscreenToggle'].el_.removeAttribute('title')
        })

        this.on('seekForward', (event, seekStep) => {
          let duration = theComponent.duration
          let futureTime = this.currentTime() + seekStep
          console.log(theComponent.videoInfos.categories)
          if (futureTime > duration && theComponent.videoInfos.categories === 2) { // ?????????????????????
            theComponent.goToNextVideo()
          } else {
            this.currentTime(this.currentTime() + seekStep)
          }
        })
        // tooltip dom
        const toolTipEl = this.controlBar.childNameIndex_.Tooltip.el_
        this.on('volumechange', function(e) {
          if (theComponent.initialVolumeChange) {
            theComponent.initialVolumeChange = false
            return
          }
          const value = this.muted() ? '0.00' : this.controlBar.volumeControl.volumeBar.el_.getAttribute('aria-valuenow')
          const volumeBarLeft = this.controlBar.childNameIndex_.VolumeControl.el_.offsetLeft // ????????????????????????
          toolTipEl.innerHTML = value.split('.')[0] + '%'
          toolTipEl.style.left = (volumeBarLeft + +value - 15) + 'px'
          toolTipEl.style.display = 'block'
          theComponent.hideToolTip() // hideToolTip function was debounced.
          storage.set('playerOptions', { volume: value * 100 / 10000 })
          this.controlBar.muteToggle.el_.removeAttribute('title')
        })

        function playToggleTooltip (text) {
          toolTipEl.innerHTML = text
          toolTipEl.style.left = 0
          toolTipEl.style.display = 'block'
        }
        // playToggle Event
        this.controlBar.playToggle.el_.addEventListener('click', function() {
          if (videojs.hasClass(this, 'vjs-paused')) {
            playToggleTooltip('????????????')
          } else {
            playToggleTooltip('????????????')
          }
          theComponent.hideToolTip()
        })

        // muteToggle Event
        this.controlBar.muteToggle.el_.addEventListener('click', function() {
          if (theComponent.player.muted() && theComponent.player.volume() <= 0.001) {
            theComponent.player.muted(false)
            theComponent.player.volume(0.5)
          }
        })

        // ???????????????hover
        const hoverButtons = { 'playToggle': '', 'NextButton': '?????????', 'muteToggle': '??????', 'fullscreenToggle': '??????' }
        let tipTimer = null
        for (let key in hoverButtons) {
          if (!this.controlBar[key]) continue
          this.controlBar[key].el_.addEventListener('mouseover', (e) => {
            this.controlBar[key].el_.removeAttribute('title')
            const leftWidth = this.controlBar[key].el_.offsetLeft
            let text = hoverButtons[key]
            if (this.controlBar[key].name_ === 'PlayToggle') {
              if (this.controlBar[key].hasClass('vjs-paused')) {
                text = '????????????'
              } else {
                text = '????????????'
              }
            }
            if (this.controlBar[key].name_ === 'FullscreenToggle') {
              if (this.isFullscreen()) {
                text = '????????????'
              } else {
                text = '??????'
              }
            }

            tipTimer = setTimeout(() => {
              toolTipEl.innerHTML = text
              if (this.controlBar[key].name_ === 'FullscreenToggle') {
                if (this.isFullscreen()) {
                  toolTipEl.style.left = (leftWidth - 20) + 'px'
                } else {
                  toolTipEl.style.left = leftWidth + 'px'
                }
              } else {
                toolTipEl.style.left = leftWidth + 'px'
              }
              toolTipEl.style.display = 'block'
            }, 500)
            theComponent.hideToolTip()
          })
          this.controlBar[key].el_.addEventListener('mouseleave', (e) => {
            clearTimeout(tipTimer)
            toolTipEl.style.display = 'none'
          })
        }

        this.hotkeys({
          enableModifiersForNumbers: false,
          enableVolumeScroll: false,
          alwaysCaptureHotkeys: true, // Forces the capture of hotkeys
        })
      })

      this.addControlbarItems(this.player)
      // sources?????? [{ src: '', type: '', label: '??????', res: 720 }]
      // m3u8
      if (!ua.ie10) {
        this.player.videoJsHlsSwitcher({
          dynamicLabel: true
        })
      } else { // mp4
        this.player.videoJsResolutionSwitcher({
          default: 'high',
          dynamicLabel: true
        })
      }
    },
    getMP4Index(mp4SourcesList) {
      if (storage.get('markedHlsLevel')) {
        let markedHlsLevel = storage.get('markedHlsLevel')
        return markedHlsLevel
      } else {
        return mp4SourcesList[0].res
      }
    },
    getHlsIndex(hlsSourcesList) {
      let index = 0
      if (storage.get('markedHlsLevel')) {
        let markedHlsLevel = storage.get('markedHlsLevel')
        hlsSourcesList.forEach(function(item, idx) {
          if (item.res === markedHlsLevel) {
            index = idx
          }
        })
      }
      return index
    },
    hideResolutionChangeTip: debounce(function() {
      this.player.childNameIndex_.ResChangeTip.hide()
    }, 2000),
    hideToolTip: debounce(function() {
      const toolTipEl = this.player.controlBar.childNameIndex_.Tooltip.el_
      toolTipEl.style.display = 'none'
    }, 2000),
    setCurrentTimeLabel(currentTime) {
      let hhMMSS = this.formatTime(currentTime)
      // set current time label
      this.player.controlBar.currentTimeDisplay.el_.childNodes[0].childNodes[1].nodeValue = hhMMSS
    },
    formatTime(seconds, guide = seconds) {
      seconds = seconds < 0 ? 0 : seconds
      let s = Math.floor(seconds % 60)
      let m = Math.floor(seconds / 60 % 60)
      let h = Math.floor(seconds / 3600)
      const gm = Math.floor(guide / 60 % 60)
      const gh = Math.floor(guide / 3600)
        // handle invalid times
      if (isNaN(seconds) || seconds === Infinity) {
        // '-' is false for all relational operators (e.g. <, >=) so this setting
        // will add the minimum number of fields specified by the guide
        h = m = s = '-'
      }
        // Check if we need to show hours
      h = (h > 0 || gh > 0) ? h + ':' : ''
        // If hours are showing, we may need to add a leading zero.
      // Always show at least one digit of minutes.
      m = (((h || gm >= 10) && m < 10) ? '0' + m : m) + ':'
        // Check if leading zero is need for seconds
      s = (s < 10) ? '0' + s : s

      let durationTextNodeValue = this.player.controlBar.durationDisplay.el_.childNodes[0].childNodes[1].nodeValue
      let repeatedTimes = durationTextNodeValue.replace(/[^:]/g, '').length
      if (repeatedTimes === 2 && !h) { // if has hours (mostly it's a movie)
        h = '0:'
      }

      let hms = h + m + s

      return hms
    },
    setInitCrtTimeValue(player) {
      setTimeout(() => {
        let durationTextNode = player.controlBar.durationDisplay.el_.childNodes[0].childNodes[1]
        let reg = /\d/g
        let text = durationTextNode.nodeValue.replace(reg, '0')
        let crtTimeTextNode = player.controlBar.currentTimeDisplay.el_.childNodes[0].childNodes[1]
        crtTimeTextNode.nodeValue = text
      }, 0)
    },
    compareResolutions(a, b) {
      if (!a.res || !b.res) { return 0 }
      return (+(b.res).slice(0, -1)) - (+(a.res).slice(0, -1))
    },
    changeXuanjiPanel() {
      this.isFullPanel = true
      this.player.el_.appendChild(document.querySelector('.right-panel'))
      this.player.controlBar.el_.style.zIndex = 2 // ?????????controlBar???panel???
      this.isPanelDisplay = false
      this.player.childNameIndex_.ControlBar.xuanjiButton.removeClass('active')
    },
    restoreXuanjiPanel() {
      this.isFullPanel = false
      let righPanelEle = this.player.el_.querySelector('.right-panel')
      this.player.el_.parentElement.appendChild(righPanelEle)
      this.isPanelDisplay = this.isPanelDisplay_saved
    },
    dispose() {
      videojs(this.$el).dispose()
      delete this.player
    },
    showMiniPlayerEls() {
      this.player.childNameIndex_.MiniplayerHeader.show()
      this.player.childNameIndex_.MiniplayerPlayToggle.show()
    },
    hideMiniPlayerHeader() {
      this.player.childNameIndex_.MiniplayerHeader.hide()
      this.player.childNameIndex_.MiniplayerPlayToggle.hide()
    },
    mousemoveEvent() {
      this.showMiniPlayerEls()
      let self = this
      clearTimeout(this.timer) // ???????????????????????????????????????????????????
      this.timer = setTimeout(function() {
        self.hideMiniPlayerHeader()
      }, 3000)
    },
    miniTogglePlay() {
      if (this.player.paused()) {
        this.player.play()
      } else {
        this.player.pause()
      }
    },
    showMiniPlayer() {
      if (!this.player) return
      if (this.manualMiniClose) return
      const scrollTop = document.body.scrollTop || document.documentElement.scrollTop
      if (scrollTop > 500) {
        this.player.controls(false)
        this.player.width(380)
        this.player.height(214)
        this.player.el().style.zIndex = 2

        this.player.el().addEventListener('mousemove', this.mousemoveEvent)
        this.player.el().addEventListener('mouseleave', () => {
          this.hideMiniPlayerHeader()
        })
        this.player.childNameIndex_.MiniplayerHeader.el_.querySelector('.close').addEventListener('click', () => {
          this.manualMiniClose = true
          this.closeMiniPlayer()
        })
        if (!this.dragBinded) {
          this.player.el().style.position = 'fixed'
          this.player.el().style.left = `${document.documentElement.clientWidth - 500}px`
          this.player.el().style.top = `${(screen.height * 0.5)}px`
          this.dragBinded = true
          draggable(this.player.el(), true)
        }
      } else {
        this.dragBinded = false
        this.closeMiniPlayer()
      }
    },
    closeMiniPlayer() {
      this.player.controls(true)
      this.player.width(this.options.width)
      this.player.height(490)
      this.player.el().style.position = 'relative'
      this.player.el().style.left = 0
      this.player.el().style.top = 0
      this.player.el().style.zIndex = 1
      draggable(this.player.el(), false)
      this.player.el().removeEventListener('mousemove', this.mousemoveEvent)
      this.hideMiniPlayerHeader()
    }
  }
}
</script>

<style lang="postcss" module>
.video-wrap {
  background: #161616;
  height: 100%;
  position: relative;
}
.video-content {
  margin: 0 auto;
  float: left;
  outline: none;
}
</style>



