<template>
<div
  :class="$style.carousel"
  :style="{
    width: `${width}px`,
    marginTop: `${top}px`
  }"
>
  <transition name="slide-fade-left">
    <div
      :class="$style.carouselToLeft"
      v-if="currShow > 0"
      @click="carouselToLeft"
    >
      <i class="iconfont icon-toprev"></i>
    </div>
  </transition>
  <transition name="slide-fade-right">
    <div
      :class="$style.carouselToRight"
      v-if="currShow < picPage - 1"
      @click="carouselToRight"
    >
      <i class="iconfont icon-tonext"></i>
    </div>
  </transition>
  <ul
    :class="$style.carouselWrap"
    :style="{
      width: `${picLength * (214 + 20)}px`,
      msTransform: `translateX(${ translate }px)`,
      webkitTransform: `translateX(${ translate }px)`,
      transform: `translateX(${ translate }px)`
    }"
  >
    <slot name="item"></slot>
  </ul>
</div>
</template>

<script>
import carouselItem from './carousel-item.vue'

export default {
  name: 'e-carousel',

  components: {
    'carousel-item': carouselItem
  },

  props: {
    width: {
      type: Number,
      default: 1150,
    },
    top: {
      type: Number,
      default: 15,
    },
    picLength: {
      type: Number,
      default: 10,
    },
  },

  data() {
    return {
      carouselWidth: 1170, // ???????????????
      translate: 0,
      currShow: 0,
    }
  },

  methods: {
    carouselToLeft() {
      if (this.currShow > 0) {
        this.currShow --
      }
    },
    carouselToRight() {
      if (this.currShow < this.picPage - 1) {
        this.currShow ++
      }
    },
  },

  mounted() {
  },

  watch: {
    currShow(newVal) {
      if (newVal === this.picPage - 1) {
        this.translate = -newVal * this.carouselWidth + (5 - this.picRemainder) * (214 + 20)
      } else {
        this.translate = -newVal * this.carouselWidth
      }
    }
  },

  computed: {
    picNum() {
      return this.$props.picLength
    },
    picPage() {
      return Math.ceil(this.picNum / 5)
    },
    picRemainder() {
      return this.picNum % 5 === 0 ? 5 : this.picNum % 5
    }
  },
}
</script>

<style lang="postcss" module>
:global(.slide-fade-left-enter-active), :global(.slide-fade-right-enter-active) {
  transition: all .3s ease;
}
:global(.slide-fade-left-leave-active), :global(.slide-fade-right-leave-active) {
  transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
:global(.slide-fade-left-enter), :global(.slide-fade-left-leave-active) {
  transform: translateX(-10px);
  opacity: 0;
}
:global(.slide-fade-right-enter), :global(.slide-fade-right-leave-active) {
  transform: translateX(10px);
  opacity: 0;
}
.carousel {
  overflow: hidden;
  position: relative;
  width: 1150px;
  height: 380px;
  margin: 0 auto;
  .carouselToLeft, .carouselToRight {
    position: absolute;
    width: 50px;
    height: 321px;
    top: 0;
    background: rgba(0, 0, 0, 0.4);
    z-index: 1;
    cursor: pointer;
    &:hover i {
      color: #FABE00;
    }
    i {
      display: block;
      color: #ccc;
      font-size: 35px;
      line-height: 321px;
      text-align: center;
      transition: .3s;
    }
  }
  .carouselToLeft {
    left: 0;
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.6), transparent);
  }
  .carouselToRight {
    right: 0;
    background: linear-gradient(90deg, transparent, rgba(0, 0, 0, 0.6));
  }
  .carouselWrap {
    overflow: hidden;
    height: 100%;
    transition: .5s ease-in-out;
  }
}
</style>



